/** axios封装
 *  -- 请求拦截、响应拦截、错误统一处理
 */
import axios from 'axios'
import QS from 'qs'
import store from '../store/index'

let baseURL = '';
if (process.env.NODE_ENV === 'development') {
    baseURL = '/api' // 开发环境
    console.log('开发环境：'+ baseURL)
} else if (process.env.NODE_ENV === 'testing') {
    baseURL = 'http://60.174.226.164:9036/BPMAPI/api' // 测试环境
    console.log('测试环境：'+ baseURL)
} else if (process.env.NODE_ENV === 'production') {
    baseURL = 'http://123.206.20.114:9036/BPMAPI/api' // 正式环境
    console.log('生产环境：'+ baseURL)
}

axios.defaults.baseURL = baseURL

// 请求超时时间
axios.defaults.timeout = 10000;
// 请求拦截器
axios.interceptors.request.use( 
 config => {
  // 每次发送请求之前判断是否存在Access_token，如果存在，则统一在http请求的header都加上Access_token
  // 即使本地存在Access_token，也有可能Access_token是过期的，所以在响应拦截器中要对返回状态进行判断
  let Access_token = sessionStorage.getItem("accessToken");
  Access_token && (config.headers.Authorization = Access_token) && (config.headers.RequestVerificationToken = 'Admin~20108');  
  return config; 
 }, 
 error => {  
  return Promise.error(error); 
 })

// 响应拦截器
axios.interceptors.response.use( 
 response => {
  if (response.status == 200 || response.status == 201) {   
   return Promise.resolve(response);  
  } else {   
   return Promise.reject(response);  
  } 
 },
 // 服务器状态码不是200的情况 
 error => {
  if (error.response.status) {   
   switch (error.response.status) {    
    // 401: 未登录    
    // 未登录则跳转登录页面，并携带当前页面的路径    
    // 在登录成功后返回当前页面，这一步需要在登录页操作。    
    case 401:
     router.replace({      
      path: '/login',      
      query: { redirect: router.currentRoute.fullPath } 
     });
     break;
    // 403 token过期    
    // 登录过期对用户进行提示    
    // 清除本地token和清空vuex中token对象    
    // 跳转登录页面    
    case 403:
     console.log('登录过期，请重新登录');    
     // 清除token     
     localStorage.removeItem('token');     
     store.commit('loginSuccess', null);     
     // 跳转登录页面，并将要浏览的页面fullPath传过去，登录成功后跳转需要访问的页面
     setTimeout(() => {      
      router.replace({       
       path: '/login',       
       query: { 
        redirect: router.currentRoute.fullPath 
       }      
      });     
     }, 1000);     
     break; 
    // 404请求不存在    
    case 404:
    console.log(网络请求不存在);    
    break;    
    // 其他错误，直接抛出错误提示    
    default:
    console.log(error.response.data.message)
   }   
   return Promise.reject(error.response);  
  }  
 }
);
/** 
 * get方法，对应get请求 
 * param {String} url [请求的url地址] 
 * param {Object} params [请求时携带的参数] 
 */
export function get(url, params){ 
    return new Promise((resolve, reject) =>{  
        axios.get(url, {   
            params: params  
        })  
        .then(res => {   
            resolve(res.data);  
        })  
        .catch(err => {   
            reject(err.data)  
        }) 
    });
}
/** 
 * post方法，对应post请求 
 * param {String} url [请求的url地址] 
 * param {Object} params [请求时携带的参数] 
 */
export function post(url, params,dataType) { 
    return new Promise((resolve, reject) => { 
        if(dataType == 'json'){
            // 融云应用服务post请求头
            axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';
            params = JSON.stringify(params);
        }else{
            // post请求头
            axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';
            params = QS.stringify(params);
        }
        axios.post(url, params)  
        .then(res => {   
            resolve(res.data);  
        })  
        .catch(err => {   
            reject(err.data)  
        }) 
    });
}
/** 
 * put方法，对应put请求 
 * param {String} url [请求的url地址] 
 * param {Object} params [请求时携带的参数] 
 */
export function put(url, params,dataType) { 
    return new Promise((resolve, reject) => { 
        if(dataType == 'json'){
            // 融云应用服务put请求头
            axios.defaults.headers.put['Content-Type'] = 'application/json;charset=UTF-8';
            params = JSON.stringify(params);
        }else{
            // put请求头
            axios.defaults.headers.put['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';
            params = QS.stringify(params);
        }
        axios.put(url, params)  
        .then(res => {
            resolve(res.data);  
        })  
        .catch(err => {
            reject(err.data);  
        }) 
    });
}
/** 
 * delete方法，对应delete请求 
 * param {String} url [请求的url地址] 
 * param {Object} params [请求时携带的参数] 
 */
export function del(url, params){ 
    return new Promise((resolve, reject) =>{  
        axios.delete(url, {   
            data: params  
        })  
        .then(res => {   
            resolve(res.data);  
        })  
        .catch(err => {   
            reject(err.data)  
        }) 
    });
}
   



