/** 
 * api接口统一管理
 */
import { get,post,put,del } from './http'

// get请求
export const axiosGet = (url,params) => get(url,params);
// psot请求
export const axiosPost = (url,params,dataType) => post(url,params,dataType);
// put请求
export const axiosPut = (url,params,dataType) => put(url,params,dataType);
// delete请求
export const axiosDel = (url,params) => del(url,params);



