
import XButton from 'vux/src/components/x-button/index.vue'
import trim from 'vux/src/tools/string/trim'

import * as util from '@/assets/js/util.js'
import { axiosPost,axiosGet } from '@/api'

export default {
name: 'ChatRoom',
props:{
groupId: String
},
data(){
return {
msgList: [],
mesInput: '',
targetUser: '',
username: '',
simId: '',
businessUuid: '',
showFace: false,
imgList: [],
size: 0,
limit:6, //限制图片上传的数量
bigImgShow: false,
recordBool: false,
kflag: false,
recorder:null,
recordText: '按住说话'
}
},
mounted(){
this.businessUuid = this.$route.query.id;
this.username = sessionStorage.getItem('userId');
this.simId = sessionStorage.getItem('simId');
this.rongInit();


document.body.addEventListener('touchstart', function(){});


if (navigator.mediaDevices === undefined) {
alert('您的浏览器没有实现 mediaDevices');
navigator.mediaDevices = {};
}

let promise = navigator.mediaDevices.getUserMedia({audio:true});
let _this = this;
promise.then(function(stream){
_this.recorder = new MediaRecorder(stream);
});


promise.catch(function(error){
alert(error.name + ": " + error.message);
});
},
components:{
XButton,
trim
},
methods:{
rongInit(){
let _this = this;


RongIMLib.RongIMClient.init("3argexb63q41e");




RongIMClient.setConnectionStatusListener({
onChanged: function (status) {
switch (status) {
case RongIMLib.ConnectionStatus.CONNECTED:
console.log('链接成功');
break;
case RongIMLib.ConnectionStatus.CONNECTING:
console.log('正在链接');
break;
case RongIMLib.ConnectionStatus.DISCONNECTED:
console.log('断开连接');
break;
case RongIMLib.ConnectionStatus.KICKED_OFFLINE_BY_OTHER_CLIENT:
console.log('其他设备登录');
break;
case RongIMLib.ConnectionStatus.DOMAIN_INCORRECT:
console.log('域名不正确');
break;
case RongIMLib.ConnectionStatus.NETWORK_UNAVAILABLE:
console.log('网络不可用');
break;
}
}
});


RongIMClient.setOnReceiveMessageListener({

onReceived: function (message){

switch(message.messageType){
case RongIMClient.MessageType.TextMessage:

message.sentTime = util.timestampToTime(message.sentTime);
_this.msgList.push(message);

break;
case RongIMClient.MessageType.VoiceMessage:


break;
case RongIMClient.MessageType.ImageMessage:


break;
case RongIMClient.MessageType.DiscussionNotificationMessage:

break;
case RongIMClient.MessageType.LocationMessage:



break;
case RongIMClient.MessageType.RichContentMessage:



break;
case RongIMClient.MessageType.InformationNotificationMessage:

break;
case RongIMClient.MessageType.ContactNotificationMessage:

break;
case RongIMClient.MessageType.ProfileNotificationMessage:

break;
case RongIMClient.MessageType.CommandNotificationMessage:

break;
case RongIMClient.MessageType.CommandMessage:

break;
case RongIMClient.MessageType.UnknownMessage:

break;
default:

}
}
});



var token = sessionStorage.getItem("token_rongyun");

RongIMClient.connect(token, {
onSuccess: function(userId) {
console.log("Connect successfully.当前用户id：" + userId);
},
onTokenIncorrect: function() {
console.log('token无效');
},
onError:function(errorCode){
var info = '';
switch (errorCode) {
case RongIMLib.ErrorCode.TIMEOUT:
info = '超时';
break;
case RongIMLib.ConnectionState.UNACCEPTABLE_PAROTOCOL_VERSION:
info = '不可接受的协议版本';
break;
case RongIMLib.ConnectionState.IDENTIFIER_REJECTED:
info = 'appkey不正确';
break;
case RongIMLib.ConnectionState.SERVER_UNAVAILABLE:
info = '服务器不可用';
break;
}
console.log(errorCode);
}
});







},

sendMsg(){

var msgItem = trim(this.mesInput);
if(!msgItem){
this.$vux.toast.text('消息不能为空')
return false;
}






































var msg = new RongIMLib.TextMessage({content:msgItem,extra:""});
var conversationtype = RongIMLib.ConversationType.GROUP; 


var targetId = this.groupId;

var _this = this;

RongIMClient.getInstance().sendMessage(conversationtype, targetId, msg, {

onSuccess: function (message) {


message.sentTime = util.timestampToTime(message.sentTime);

_this.msgList.push(message);
_this.mesInput = '';
},
onError: function (errorCode, message){
var info = '';
switch (errorCode) {
case RongIMLib.ErrorCode.TIMEOUT:
info = '超时';
break;
case RongIMLib.ErrorCode.UNKNOWN_ERROR:
info = '未知错误';
break;
case RongIMLib.ErrorCode.REJECTED_BY_BLACKLIST:
info = '在黑名单中，无法向对方发送消息';
break;
case RongIMLib.ErrorCode.NOT_IN_DISCUSSION:
info = '不在讨论组中';
break;
case RongIMLib.ErrorCode.NOT_IN_GROUP:
info = '不在群组中';
break;
case RongIMLib.ErrorCode.NOT_IN_CHATROOM:
info = '不在聊天室中';
break;
default :
info = "x";
break;
}
_this.$vux.toast.text('发送失败:' + info);
}
});
this.$nextTick(() => {

let container =  this.$refs.dialogueContent;
container.scrollTop = container.scrollHeight;
});
},
sendImg(base64Str,imageUri){
/*
图片转为可以使用 HTML5 的 FileReader 或者 canvas 也可以上传到后台进行转换。

注意事项：
1、缩略图必须是 base64 码的 jpg 图。
2、不带前缀。
3、大小建议不超过 100 K。
*/


var msg = new RongIMLib.ImageMessage({content:base64Str,imageUri:imageUri});
var conversationtype = RongIMLib.ConversationType.GROUP; 
var targetId = this.groupId; 
var _this = this;
RongIMClient.getInstance().sendMessage(conversationtype, targetId, msg, {
onSuccess: function (message) {


message.sentTime = util.timestampToTime(message.sentTime);
_this.msgList.push(message);
},
onError: function (errorCode,message) {
var info = '';
switch (errorCode) {
case RongIMLib.ErrorCode.TIMEOUT:
info = '超时';
break;
case RongIMLib.ErrorCode.UNKNOWN_ERROR:
info = '未知错误';
break;
case RongIMLib.ErrorCode.REJECTED_BY_BLACKLIST:
info = '在黑名单中，无法向对方发送消息';
break;
case RongIMLib.ErrorCode.NOT_IN_DISCUSSION:
info = '不在讨论组中';
break;
case RongIMLib.ErrorCode.NOT_IN_GROUP:
info = '不在群组中';
break;
case RongIMLib.ErrorCode.NOT_IN_CHATROOM:
info = '不在聊天室中';
break;
default :
info = x;
break;
}
console.log('发送失败:' + info);
}
});
},
sendRecord(base64Str){
/*
{
"content":"bhZPzJXimRwrtvc=",
"duration":7,
"extra":""
}
说明：
content	语音内容，格式为 AMR，以 Base64 进行 Encode 后需要将所有 \r\n 和 \r 和 \n 替换成空，大小建议不超过 60k。
duration	语音消息的时长，最长为 60 秒（单位：秒）。
extra	扩展信息，可以放置任意的数据内容，也可以去掉此属性。
*/
var msg = new RongIMLib.VoiceMessage({content:base64Str,duration:'',extra:''});
var conversationtype = RongIMLib.ConversationType.GROUP; 
var targetId = this.groupId; 
var _this = this;
RongIMClient.getInstance().sendMessage(conversationtype, targetId, msg, {
onSuccess: function (message) {


message.sentTime = util.timestampToTime(message.sentTime);
_this.msgList.push(message);
},
onError: function (errorCode,message) {
var info = '';
switch (errorCode) {
case RongIMLib.ErrorCode.TIMEOUT:
info = '超时';
break;
case RongIMLib.ErrorCode.UNKNOWN_ERROR:
info = '未知错误';
break;
case RongIMLib.ErrorCode.REJECTED_BY_BLACKLIST:
info = '在黑名单中，无法向对方发送消息';
break;
case RongIMLib.ErrorCode.NOT_IN_DISCUSSION:
info = '不在讨论组中';
break;
case RongIMLib.ErrorCode.NOT_IN_GROUP:
info = '不在群组中';
break;
case RongIMLib.ErrorCode.NOT_IN_CHATROOM:
info = '不在聊天室中';
break;
default :
info = x;
break;
}
console.log('发送失败:' + info);
}
});
},
getHistoryMsg(){
var _this = this;


var targetId = this.groupId;

RongIMClient.getInstance().getHistoryMessages(RongIMLib.ConversationType.GROUP, targetId, null, 10, {
onSuccess: function(list, hasMsg) {


if(list.length == 0){
_this.$vux.toast.text('暂无更多历史消息');
}
var filter_list = list.filter((item,index) => {
return item.offLineMessage
})

filter_list.reverse().forEach(item => {
item.sentTime = util.timestampToTime(item.sentTime);
_this.msgList.unshift(item);
});
},
onError: function(error) {


console.log("GetHistoryMessages,errorcode:" + error);
}
});
},
audioPlay(){
let _this = this;
RongIMLib.RongIMVoice.init();
/*
音频格式: base64 格式的 AMR
完整示例音频: https://cdn.ronghub.com/voice-amr-base64.json
*/

var duration = _this.audioFile.length / 1024;

RongIMLib.RongIMVoice.preLoaded(_this.audioFile, function(){

RongIMLib.RongIMVoice.play(_this.audioFile, duration);
});
},
audioStop(){
let _this = this;
RongIMLib.RongIMVoice.stop(_this.audioFile);
},




chooseType(){
this.$refs.upload_file.click();
},
fileChange(el){
if (!el.target.files[0].size) return;
this.fileList(el.target);
el.target.value = '';
},
fileList(fileList) {
let files = fileList.files;
for (let i = 0; i < files.length; i++) {

if (files[i].type != '') {
let imgListName = [];
this.imgList.forEach(item => {
imgListName.push(item.file.name)
})
if(imgListName.indexOf(files[i].name) == -1){
this.fileAdd(files[i]);
}
} else {

this.folders(fileList.items[i]);
}
}
},

folders(files) {
let _this = this;

if (files.kind) {
files = files.webkitGetAsEntry();
}
files.createReader().readEntries(function (file) {
for (let i = 0; i < file.length; i++) {
if (file[i].isFile) {
_this.foldersAdd(file[i]);
} else {
_this.folders(file[i]);
}
}
});

},
foldersAdd(entry) {
let _this = this;
entry.file(function (file) {
_this.fileAdd(file)
})
},
fileAdd(file) {
if (this.limit !== undefined) this.limit--;
if (this.limit !== undefined && this.limit < 0) return;

this.size = this.size + file.size;

if (file.type.indexOf('image') == -1) {
this.$vux.toast.text('请选择图片文件')
} else {
let reader = new FileReader();
let image = new Image();
let _this = this;

let canvas = document.createElement('canvas');
let context = canvas.getContext('2d');

reader.readAsDataURL(file); 

reader.onload = function (e) {

file.src = e.target.result;
image.onload = function(){
let width = image.width;
let height = image.height;
file.width = width;
file.height = height;

let originWidth = width;
let originHeight = height;

let maxWidth = 400, maxHeight = 400;

let targetWidth = originWidth, targetHeight = originHeight;

if (originWidth > maxWidth || originHeight > maxHeight) {
if (originWidth / originHeight > maxWidth / maxHeight) {

targetWidth = maxWidth;
targetHeight = Math.round(maxWidth * (originHeight / originWidth));
} else {
targetHeight = maxHeight;
targetWidth = Math.round(maxHeight * (originWidth / originHeight));
}
}

canvas.width = targetWidth;
canvas.height = targetHeight;

context.clearRect(0, 0, targetWidth, targetHeight);

context.drawImage(image, 0, 0, targetWidth, targetHeight);


canvas.toBlob(function (blob) {
let url = URL.createObjectURL(blob);
let img64 = canvas.toDataURL("image/jpeg", 0.5);




_this.sendImg(url,img64);
}, file.type || 'image/png');

_this.imgList.push({
file
});
};
image.src = file.src;
}
}
},
displayImg(e){
this.bigImgShow = !this.bigImgShow;
this.$refs.bigImg.src = e.currentTarget.src;
document.querySelector('.vux-slider').setAttribute('style', 'z-index:999');
},
hideImg(){
this.bigImgShow = !this.bigImgShow;
document.querySelector('.vux-slider').setAttribute('style', 'z-index:0');
},
showVoiceBtn(){
this.recordBool ? this.recordBool=false : this.recordBool=true;
},
recordTouchStart(){
if(this.kflag){
e.preventDefault();
}else{
this.recorder.start();
this.recordText = '松开结束';
this.kflag = true;
}
},
recordTouchEnd(){
this.recorder.stop();
this.recordText = '按住说话';
this.kflag = false;
let _this = this;
this.recorder.ondataavailable = function(){

var reader = new FileReader();
reader.readAsDataURL(event.data);
reader.onload = function(){

let base64 = reader.result; 
_this.sendRecord(base64);
};
}
},
recordPlay(e){
let src = e.target.getAttribute('data-src');
let audio = document.getElementById('audio');
audio.src = src;
audio.play();
}
}
}



