
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Group from 'vux/src/components/group/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'

import GroupChat from '@/components/GroupChat'
import GroupMemberSelect from '@/components/GroupMemberSelect'
import { axiosPost,axiosGet } from '@/api'

export default {
name: 'AddCustomer',
directives: {
TransferDom
},
data(){
return{
username: '',
simId: '',
custMguid: '',
custName: '',
masterList: [],
bankInfoList: [],
bankInfoBool:true,
loading:true,
showMenus:false,
menus: {
addBsn: '<span>商机添加</span>',
editCust: '<span>客户编辑</span>',
addBank: '<span>银行添加</span>',
},
show: false
}
},
mounted() {
this.username = sessionStorage.getItem("userId");
this.simId = sessionStorage.getItem("simId");
this.custMguid = this.$route.query.id;
let params = {
url: '/BPMTable',
data:{
TID:'CustInfoManager',
GID:'Master',
RID:'M5',
SID:'S2',
UserName:this.username,
SIMID: this.simId,
MGUID: this.custMguid,
DGUID:'',
Orderbycolum:'',
Querystring:'',
PageIndex:1,
PageSize:10
}
}
axiosGet(params.url,params.data).then(res => {
this.custName = res[0].CustLeader;
this.masterList = [
{
title:'客户编号：',
data: res[0].CustId
},
{
title: '公司名称：',
data: res[0].CustName
},
{
title: '客户地址：',
data: res[0].CustAddress
},
{
title: '客户类型：',
data: res[0].CustInfoManager_FCustClass,
},
{
title: '产品类别：',
data: res[0].CustInfoManager_CPLB
},
{
title: '品牌：',
data: res[0].CustInfoManager_PinPai
},
{
title: '区域影响客户：',
data: res[0].CustInfoManager_KHYXL
},
{
title: '喜好：',
data: res[0].XiHao
},
{
title: '职位：',
data: res[0].CustInfoManager_DLSSF
},
{
title: '姓名：',
data: res[0].CustLeader
},
{
title: '手机：',
data: res[0].CustMobile
},
{
title: '手机2：',
data: res[0].CustMobile1
},
{
title: '客户来源：',
data: res[0].CustInfoManager_FCustLY
},
{
title: 'QQ号：',
data: res[0].QQ
},
{
title: '微信号：',
data: res[0].WXH
},
{
title: '身份证号码：',
data: res[0].SFZNo
},
{
title: '所属省份：',
data: res[0].CustInfoManager_CustDQ
},
{
title: '所属城市：',
data: res[0].CustInfoManager_CustCity
},
{
title: '业务员：',
data: res[0].CustInfoManager_CustSaleMan
},
{
title: '备注：',
data: res[0].CustMemo
}
]
this.loading = false;

},err => {
this.$vux.toast.text(err.Message)
})


let params_bank_info = {
url: '/BPMTable',
data:{
TID:'CustInfoManager',
GID:'Detail1',
RID:'M5',
SID:'S2',
UserName: this.username,
SIMID: this.simId,
MGUID: this.custMguid,
DGUID:'',
Orderbycolum:'',
Querystring:'',
PageIndex:1,
PageSize:20
}
}
axiosGet(params_bank_info.url,params_bank_info.data).then(res => {
this.bankInfoList = res;
this.bankInfoList.length == 0 ? this.bankInfoBool=false : this.bankInfoBool=true;
},err => {
this.$vux.toast.text(err.Message)
})
},
components:{ Flexbox,FlexboxItem,Swiper,SwiperItem,Tab,TabItem,GroupChat,GroupMemberSelect,Actionsheet,TransferDom,Group,Popup,PopupHeader },
methods:{
menuListClick(key) {
sessionStorage.setItem('custMguid',this.custMguid);
switch(key){
case 'addBsn':
sessionStorage.setItem('custNameForBsn',this.custName);
this.show = true;
break;
case 'editCust':
sessionStorage.setItem('custEditTitle','客户编辑');
this.$router.push({path: '/addCustomer'});
break;
case 'addBank':
this.$router.push({path: '/addBankInfo'});
break;
default:
return
}
},
onCancel(){
this.show = false;
},
back(){
this.$router.push({path: '/customer'})
}
}
}



