
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Search from 'vux/src/components/search/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import * as util from '@/assets/js/util.js'
import { axiosPost,axiosGet } from '@/api'

export default {
name: 'SelectSearch',
directives: {
TransferDom
},
components: {Popup,Search,Flexbox,FlexboxItem},
data(){
return {
username:'',
simId: '',
show: false,
results: [],
dynamicResults: [],
value: '',
valChoosed: '',
valShow: '',
noData: false,
pageIndex: '1',
masterList: []
}
},
created(){
util.EventUtil.listenTouchDirection(document, false, this.touchUp, '', '', '')
},
mounted(){
this.username = sessionStorage.getItem('userId');
this.simId = sessionStorage.getItem('simId');
this.valShow = sessionStorage.getItem('machineNum');
this.fetchData();
},
methods:{
fetchData(queryArg){

let machineParams = {
url: '/BPMTable',
data: {
TID: 'ESSBInventory',
GID: 'Master',
RID: 'S1',
SID: 'YHJJ_YD',
UserName: this.username,
Orderbycolum: '',
Querystring: queryArg || '',
pageIndex: this.pageIndex,
PageSize: '50',
SIMID: this.simId,
MGUID: '',
DGUID: ''
}
}
axiosGet(machineParams.url,machineParams.data).then(res => {
this.masterList = res;
},err => {
this.$vux.toast.text(err.Message);
})
},
getResult(val){

this.pageIndex = '1';
if(val){
this.fetchData('IJQXLH%7c'+val);
}else{
this.fetchData();
}
},
touchUp(){
let container = document.getElementById("search-box");
let distance = container.scrollHeight - container.scrollTop;
if(this.value && distance < 1000){
this.pageIndex++;

let machineParams = {
url: '/BPMTable',
data: {
TID: 'ESSBInventory',
GID: 'Master',
RID: 'S1',
SID: 'YHJJ_YD',
UserName: this.username,
Orderbycolum: '',
Querystring: 'IJQXLH%7c'+this.value,
pageIndex: this.pageIndex,
PageSize: '10',
SIMID: this.simId,
MGUID: '',
DGUID: ''
}
}
this.$vux.loading.show({
text: '正在加载...'
})
axiosGet(machineParams.url,machineParams.data).then(res => {
this.$vux.loading.hide();
if(res.length != 0){
this.masterList = this.masterList.concat(res);
}else{
this.$vux.toast.text('没有更多数据');
}
},err => {
this.$vux.loading.hide();
this.$vux.toast.text(err.Message);
})
}
},
resultClick(arg){
this.valShow = arg;
this.$emit('valChange',arg);
this.show = false;
},
showPop(){
this.show = true;
},
onCancel(){
this.show = false;
}
}
}



