
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import CustomerSelect from '@/components/CustomerSelect'
import MachineNumSelect from '@/components/MachineNumSelect'
import { axiosPost,axiosGet,axiosPut } from '@/api'

export default {
name: 'AddBusiness',
data(){
return{
username: '',
simId: '',
bsnMguid: '',
CustName: '', // 公司名称
deviceTypeList: [], //设备类型
deviceType: '',
deviceParamsArr: [],
demandBrandsList: [], //需求品牌
demandBrands: '',
demandBrands2: '',
demandBrands3: '',
dateProductionList: [], //出厂年限
dateProduction: '',
serviceTimeList: [], //使用时间
serviceTime: '',
certificateList: [{key:'8310', value:'有'},{key:'8320', value:'无'}], //有无合格证
certificateBool: '',
invoiceList: [{key:'8210', value:'有'},{key:'8220', value:'无'}], //有无发票
invoiceBool: '',
priceSection: [], //价格区间
price: '',
machineNum: '',
transPrice: '',
remark: '' 
}
},
components:{GroupTitle, Group, Cell, XInput, Selector, PopupPicker, XNumber, XTextarea, CustomerSelect,MachineNumSelect},
mounted(){
this.username = sessionStorage.getItem('userId');
this.simId = sessionStorage.getItem('simId');
this.bsnMguid = this.$route.query.id;

let deviceParams = {
url: '/BPMTable',
data: {
TID: 'InvenItemClass',
GID: 'Master',
RID: 's1',
SID: 's1',
UserName: this.username,
SIMID: this.simId,
MGUID: '',
DGUID: '',
OrderByColum: '',
QueryString: '',
PageIndex: '1',
PageSize: '100'
}
}
axiosGet(deviceParams.url,deviceParams.data).then(res => {
res.forEach((item) => {
let temp = {};
temp.key = item.ItemClassId +' '+ item.MGUID;
temp.value = item.ItemClassDesc;
this.deviceTypeList.push(temp);
});
},err => {
this.$vux.toast.text(err.Message)
})

let productionParams = {
url: '/BPMTable',
data: {
TID: 'V_CCSJ',
GID: 'Master',
RID: 'S1',
SID: 'WFLMaster',
UserName: this.username,
SIMID: this.simId,
MGUID: '',
DGUID: '',
OrderByColum: '',
QueryString: '',
PageIndex: '1',
PageSize: '50'
}
}
axiosGet(productionParams.url,productionParams.data).then(res => {
res.forEach((item) => {
let temp = {};
temp.key = item.CCSJ;
temp.value = item.JJ;
this.dateProductionList.push(temp);
});
},err => {
this.$vux.toast.text(err.Message)
})

let serviceTimeParams = {
url: '/BPMTable',
data: {
TID: 'GZXS',
GID: 'Master',
RID: 'R1',
SID: 's1',
UserName: this.username,
SIMID: this.simId,
MGUID: '',
DGUID: '',
OrderByColum: '',
QueryString: '',
PageIndex: '1',
PageSize: '50'
}
}
axiosGet(serviceTimeParams.url,serviceTimeParams.data).then(res => {
res.forEach((item) => {
let temp = {};
temp.key = item.ID;
temp.value = item.GZXS;
this.serviceTimeList.push(temp);
});
},err => {
this.$vux.toast.text(err.Message)
})

let priceParams = {
url: '/BPMTable',
data: {
TID: 'JGQJ',
GID: 'Master',
RID: 'R1',
SID: 's1',
UserName: 'admin',
SIMID: 'A000004F6DBABA',
MGUID: '',
DGUID: '',
OrderByColum: '',
QueryString: '',
PageIndex: '1',
PageSize: '50'
}
}
axiosGet(priceParams.url,priceParams.data).then(res => {
res.forEach((item) => {
let temp = {};
temp.key = item.Code;
temp.value = item.Prices;
this.priceSection.push(temp);
});
},err => {
this.$vux.toast.text(err.Message)
})


let bsnParams = {
url: '/BPMTable',
data:{
TID:'SellNewCompeteBsnis',
GID:'Master',
RID:'R1',
SID:'S1',
UserName:'',
Orderbycolum:'',
PageIndex:1,
PageSize:10,
SIMID: this.simId,
MGUID: this.bsnMguid,
DGUID:'',
Querystring:''
}
}
axiosGet(bsnParams.url,bsnParams.data).then(res => {
let temp = res[0];
let sblx = this.deviceTypeList.filter((item,index)=>{
let keyArr = item.key.split(' ');
return keyArr[0] == temp.SBLX;
})
this.CustName = temp.CustName; 
sblx.length != 0 ? this.deviceType=sblx[0].key : this.deviceType='';
this.demandBrands = temp.Brand;
this.demandBrands2 = temp.Probrand2 || '';
this.demandBrands3 = temp.Probrand3 || '';
this.dateProduction = temp.Pdatep;
this.serviceTime = temp.Phours;
this.certificateBool = temp.Certificate || '';
this.invoiceBool = temp.Invoice || '';
this.price = temp.Prices;
this.machineNum = temp.JQXLH || '';
this.transPrice = temp.CJJJ || '';
this.remark = temp.Memo || '';
sessionStorage.setItem('machineNum',temp.JQXLH || '');
},err => {
this.$vux.toast.text(err.Message);
})
},
methods:{
chooseDevice(){

this.deviceParamsArr = this.deviceType.split(" ");
let brandParams = {
url: '/BPMTable',
data: {
TID: 'InvenItemClass',
GID: 'Detail2',
RID: 'R1',
SID: 'S1',
UserName: this.username,
SIMID: this.simId,
MGUID: this.deviceParamsArr[1],
DGUID: '',
OrderByColum: '',
QueryString: '',
PageIndex: '1',
PageSize: '300'
}
}
axiosGet(brandParams.url,brandParams.data).then(res => {
this.demandBrandsList = [];
res.forEach((item) => {
let temp = {};
temp.key = item.NewCode;
temp.value = item.BrandClassDesc;
this.demandBrandsList.push(temp);
});
},err => {
this.$vux.toast.text(err.Message)
})
},
getMachineNum(msg){
this.machineNum = msg;
},
handleSubmit(){
let empty = '';
switch (empty){
case this.deviceType:
this.$vux.toast.text('请选择设备类型');
break;
case this.demandBrands:
this.$vux.toast.text('请选择需求品牌1');
break;
case this.dateProduction:
this.$vux.toast.text('请选择出厂年限');
break;
case this.serviceTime:
this.$vux.toast.text('请选择使用时间');
break;
case this.price:
this.$vux.toast.text('请选择价格区间');
break;
default:

let bsnEditParams = {
url: '/BPMTable',
data: {
GID: "Master",
RID: "R1",
SID: "S1",
SIMID: this.simId,
TID: "SellNewCompeteBsnis",
TIDKeyValue: this.bsnMguid,
UserMobile: "UserMobile_SellBsnisMXG",
UserName: "null",
keyandvalue: [
{
field: "SBLX",
value: this.deviceParamsArr[0]
},{
field: "Brand",
value: this.demandBrands
},{
field: "Probrand2",
value: this.demandBrands2
},{
field: "Probrand3",
value: this.demandBrands3
},{
field: "Pdatep",
value: this.dateProduction
},{
field: "phours",
value: this.serviceTime
},{
field: "Certificate",
value: this.certificateBool
},{
field: "Invoice",
value: this.invoiceBool
},{
field: "Prices",
value: this.price
},{
field: "JQXLH",
value: this.machineNum
},{
field: "CJJJ",
value: this.transPrice
},{
field: "Memo",
value: this.remark
}
]
},
dataType:'json'
}
this.$vux.loading.show({
text: '提交中...'
})
axiosPut(bsnEditParams.url,bsnEditParams.data,bsnEditParams.dataType).then(res => {
this.$vux.loading.hide();
this.$vux.toast.text('提交成功');
this.$router.go(-1);
},err => {
this.$vux.loading.hide();
this.$vux.toast.text(err.Message);
})
}
}
}
}



