
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import GroupChat from '@/components/GroupChat'
import ConsignMemberSelect from '@/components/ConsignMemberSelect'
import MachineNumSelect from '@/components/MachineNumSelect'
import { axiosPost,axiosGet,axiosPut } from '@/api'

export default {
name: 'AddBusiness',
directives: {
TransferDom
},
data(){
return{
simId: '',
username: '',
operateBool: true,
masterList: [],
bsnNo: '',
bsnMguid: '',
listTitle:['跟踪明细'],
index:0,
loading: true,
showMenus: false,
menus: {
btnEditBsn: '<span>编辑</span>',
btnLose: '<span>丢单</span>',
btnAbandon: '<span>放弃</span>',
btnShare: '<span>共享</span>',
btnGet: '<span>赢单</span>',
btnAudit: '<span>审核</span>'
},
loseModalShow: false,
abandonModalShow: false,
getModalShow: false,
auditModalShow: false
}
},
mounted(){
this.bsnMguid = this.$route.query.id;
this.username = sessionStorage.getItem('userId');
this.simId = sessionStorage.getItem('simId');
let params = {
url: '/BPMTable',
data:{
TID:'BuyNewCompeteBsnis',
GID:'Master',
RID:'R1',
SID:'S1',
UserName:'',
Orderbycolum:'',
PageIndex:1,
PageSize:10,
SIMID: this.simId,
MGUID: this.bsnMguid,
DGUID:'',
Querystring:''
}
}
axiosGet(params.url,params.data).then(res => {
this.loading = false;
this.bsnNo = res[0].BsnNo;
this.CustPhone = res[0].CustPhone;

let bsnStatus = res[0].ST;
if(bsnStatus == '丢单' || bsnStatus == '赢单' || bsnStatus == '放弃' || bsnStatus == '关闭商机'){
this.operateBool = false;
document.querySelector('.add-icon').style.display='none';
}else{
this.operateBool = true;
if(bsnStatus == '待跟踪'){
delete this.menus.btnGet;
delete this.menus.btnAudit;
}
}

this.masterList = [
{
title:'商机编号：',
data: res[0].BsnNo
},
{
title: '姓名：',
data: res[0].CustLeader
},
{
title: '客户手机：',
data: res[0].CustPhone,
type: 'phoneNumber'
},
{
title: '公司名称：',
data: res[0].CustName
},
{
title: '状态：',
data: res[0].ST
},
{
title: '紧急度：',
data: res[0].BuyNewCompeteBsnis_Bdates
},
{
title: '客户报价(元)：',
data: res[0].Bprice
},
{
title: '设备类型：',
data: res[0].BuyNewCompeteBsnis_SBLX
},
{
title: '品牌：',
data: res[0].BuyNewCompeteBsnis_Pbrands
},
{
title: '型号：',
data: res[0].Pmodel
},
{
title: '收货仓库：',
data: res[0].BuyNewCompeteBsnis_IINV
},
{
title: '出厂年限：',
data: res[0].Pdatep
},
{
title: '使用时间：',
data: res[0].Phours != -1?res[0].Phours:'未知'
},
{
title: '国家或地区：',
data: res[0].GJHDQ
},
{
title: '设备省份：',
data: res[0].SBSF
},
{
title: '设备城市：',
data: res[0].SBCS
},
{
title: '机械属性：',
data: res[0].KeyValue
},
{
title: '预估价格(万元)：',
data: res[0].Eprice
},
{
title: '寄售价格上限(元)：',
data: res[0].SXJJ
},
{
title: '商机来源：',
data: res[0].BuyNewCompeteBsnis_SJLY
},
{
title: '备注：',
data: res[0].Memo
},
{
title: '经办人：',
data: res[0].BuyNewCompeteBsnis_Puser
},
]
},err => {
this.$vux.toast.text(err.Message)
})
this.$router.afterEach((to, from) => {
this.$router.go(0);
})
},
components:{ Flexbox,FlexboxItem,Swiper,SwiperItem,Tab,TabItem,GroupChat,ConsignMemberSelect,Actionsheet,TransferDom,PopupHeader,Group,Selector,XInput,Popup,MachineNumSelect,Confirm },
methods:{

menuListClick(key) {
sessionStorage.setItem('bsnMguid',this.bsnMguid);
switch(key){
case 'btnEditBsn':
sessionStorage.setItem('bsnEditTitle','商机编辑');
this.$router.push({ path: '/consignBusinessEdit',query:{ id:this.bsnMguid }});
break;
case 'btnLose':
this.loseModalShow = true;
break;
case 'btnAbandon':
this.abandonModalShow = true;
break;
case 'btnShare':
sessionStorage.setItem("bsnNoShared",this.bsnNo);
sessionStorage.setItem("custPhoneShared",this.CustPhone);
this.$router.push({ path: '/consignBusinessShare',query:{ id:this.bsnMguid }});
break;
case 'btnGet':
this.getModalShow = true;
break;
case 'btnAudit':
this.auditModalShow = true;
break;
default:
return
}
},

loseBsnConfirm(value){
if(!value){
this.$vux.toast.text('请输入丢单原因');
return
}
this.$refs.loseModal.setInputValue('');

let bsnLoseParams = {
url: '/BPMTable',
data: {
GID: "Master",
RID: "R1",
SID: "S1",
SIMID: this.simId,
TID: "BuyNewCompeteBsnis",
TIDKeyValue: this.bsnMguid,
UserMobile: "UserMobile_BuyBsnisMDD",
UserName: this.username,
keyandvalue: [
{
field: "lostreason",
value: value
}
]
},
dataType:'json'
}
this.$vux.loading.show({
text: '提交中...'
})
axiosPut(bsnLoseParams.url,bsnLoseParams.data,bsnLoseParams.dataType).then(res => {
this.$vux.loading.hide();
this.$router.push({path: '/consignBusiness'});
this.$vux.toast.text('提交成功');
},err => {
this.$vux.loading.hide();
this.$vux.toast.text(err.Message);
})
},

abandonBsnConfirm(value){
if(!value){
this.$vux.toast.text('请输入放弃原因');
return
}
this.$refs.abandonModal.setInputValue('');

let bsnAbandonParams = {
url: '/BPMTable',
data: {
GID: "Master",
RID: "R1",
SID: "S1",
SIMID: this.simId,
TID: "BuyNewCompeteBsnis",
TIDKeyValue: this.bsnMguid,
UserMobile: "UserMobile_BuyBsnisMFQ",
UserName: this.username,
keyandvalue: [{
field:"lostreason",value:value
}]
},
dataType:'json'
}
this.$vux.loading.show({
text: '提交中...'
})
axiosPut(bsnAbandonParams.url,bsnAbandonParams.data,bsnAbandonParams.dataType).then(res => {
this.$vux.loading.hide();
this.$router.push({path:'/consignBusiness'});
this.$vux.toast.text('提交成功');
},err => {
this.$vux.loading.hide();
this.$vux.toast.text(err.Message);
})
},

getBsnConfirm(value){
if(!value){
this.$vux.toast.text('请输入成交寄售价格');
return
}
this.$refs.getModal.setInputValue('');
let bsnGetParams = {
url: '/BPMTable',
data: {
GID: "Master",
RID: "R1",
SID: "S1",
UserName: this.username,
SIMID: this.simId,
TID: "BuyNewCompeteBsnis",
TIDKeyValue: this.bsnMguid,
UserMobile: "UserMobile_BuyBsnisMYD",
keyandvalue: [
{
field:"YDR",
value:this.username
},{
field:"CJJJ",
value:value
}
]
},
dataType:'json'
}
this.$vux.loading.show({
text: '提交中...'
})
axiosPut(bsnGetParams.url,bsnGetParams.data,bsnGetParams.dataType).then(res => {
this.$vux.loading.hide();
this.$router.push({path: '/consignBusiness'});
this.$vux.toast.text('提交成功');
},err => {
this.$vux.loading.hide();
this.$vux.toast.text(err.Message);
})
},

auditConfirm(){
let sharedTime = dateFormat(new Date(), 'YYYY-MM-DD HH:mm:ss');
let bsnAuditParams = {
url: '/BPMTable',
data: {
GID: "Master",
RID: "R1",
SID: "S2",
UserName: this.username,
SIMID: this.simId,
TID: "BuyNewCompeteBsnis",
TIDKeyValue: this.bsnMguid,
UserMobile: "UserMobile_BuyMPGSH",
keyandvalue: [
{
field: "Otime",
value: sharedTime
}
]
},
dataType:'json'
}
this.$vux.loading.show({
text: '提交中...'
})
axiosPut(bsnAuditParams.url,bsnAuditParams.data,bsnAuditParams.dataType).then(res => {
this.$vux.loading.hide();
this.$router.push({path: '/consignBusiness'});
this.$vux.toast.text('提交成功');
},err => {
this.$vux.loading.hide();
this.$vux.toast.text(err.Message);
})
}
}
}



