
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import * as util from '@/assets/js/util.js'
import { axiosPost,axiosGet } from '@/api'

const pulldownDefaultConfig = {
content: '下拉刷新',
height: 40,
autoRefresh: false,
downContent: '下拉刷新',
upContent: '释放后刷新',
loadingContent: '正在刷新...',
clsPrefix: 'xs-plugin-pulldown-'
}
const pullupDefaultConfig = {
content: '上拉加载更多',
pullUpHeight: 60,
height: 40,
autoRefresh: false,
downContent: '释放后加载',
upContent: '上拉加载更多',
loadingContent: '加载中...',
clsPrefix: 'xs-plugin-pullup-'
}

export default {
name:'customer',
directives: {
TransferDom
},
data(){
return {
username: '',
simId: '',
customerList: [],
loading:true,
noData:false,
pullupDefaultConfig: pullupDefaultConfig,
pulldownDefaultConfig: pulldownDefaultConfig,
pageIndex: 1,
show: false,
custTypeList: [],
custType:'',
provinceList: [],
province: '',
cityList: [],
city: '',
custLeader: '',
custMobile: ''
}
},
components:{ Flexbox,FlexboxItem,XButton,Scroller,Popup,TransferDom,PopupHeader,Group,Selector,XInput },
mounted(){
this.username = sessionStorage.getItem('userId');
this.simId = sessionStorage.getItem('simId');
this.fetchData();

let custParams = {
url: '/BPMTable',
data: {
TID: 'CustomerType',
GID: 'Master',
RID: 'S1',
SID: 's1',
UserName: this.username,
SIMID: this.simId,
MGUID: '',
DGUID: '',
OrderByColum: '',
QueryString: '',
PageIndex: '1',
PageSize: '100'
}
}
axiosGet(custParams.url,custParams.data).then(res => {
res.forEach((item) => {
let temp = {};
temp.key = item.TypeId;
temp.value = item.TypeDesc;
this.custTypeList.push(temp);
});
},err => {
this.$vux.toast.text(err.Message)
})

let provinceParams = {
url: '/BPMTable',
data: {
TID: 'SSX_Proince',
GID: 'Master',
RID: 'R1',
SID: 'S1',
UserName: this.username,
OrderByColum: '',
QueryString: '',
PageIndex: '1',
PageSize: '100',
SIMID: this.simId,
MGUID: '',
DGUID: ''
}
}
axiosGet(provinceParams.url,provinceParams.data).then(res => {
res.forEach((item) => {
let temp = {};
temp.key = item.Value;
temp.value = item.Value;
this.provinceList.push(temp);
});
},err => {
this.$vux.toast.text(err.Message)
})
},
methods:{
fetchData(){
let params = {
url: '/BPMTable',
data:{
TID: 'CustInfoManager',
GID: 'Master',
RID: 'M2',
SID: 'S2',
UserName: this.username,
Orderbycolum: 'custId desc',
Querystring: '',
PageIndex: this.pageIndex,
PageSize: '200',
SIMID: this.simId,
MGUID: '',
DGUID: '',
}
}
axiosGet(params.url,params.data).then(res => {
this.loading = false;
this.customerList = this.customerList.concat(res);
if (this.customerList.length < 10 || res.length == 0) {
this.$refs.scrollerBottom.disablePullup();
}else{
this.$refs.scrollerBottom.enablePullup();
}
if(this.customerList.length == 0){
this.noData = true;
}else{
this.noData = false;
}
this.$nextTick(() => {
this.$refs.scrollerBottom.reset();
})
},err => {
this.$vux.toast.text(err.Message);
})
},
back(){
this.$router.push({path: '/home'})
},
refresh(){
this.pageIndex = 1;
this.customerList = [];
this.fetchData();
this.$nextTick(() => {
this.$refs.scrollerBottom.donePulldown();
})
},
loadMore(){
this.pageIndex++;
this.fetchData();
this.$nextTick(() => {
this.$refs.scrollerBottom.donePullup();
})
},
addCustomer(){
this.$router.push({path: '/addCustomer'})
},
showPop(){
this.show = true;
},
chooseCity(){

let provinceParams = {
url: '/BPMTable',
data: {
TID: 'system_city',
GID: 'Master',
RID: 'R1',
SID: 'S1',
UserName: this.username,
OrderByColum: '',
QueryString: 'shen%3a'+this.province,
PageIndex: '1',
PageSize: '100',
SIMID: this.simId,
MGUID: '',
DGUID: ''
}
}
axiosGet(provinceParams.url,provinceParams.data).then(res => {
this.cityList = [];
res.forEach((item) => {
let temp = {};
temp.key = item.c_name;
temp.value = item.c_name;
this.cityList.push(temp);
});
},err => {
this.$vux.toast.text(err.Message)
})
},
resetFunc(){
this.custType = '';
this.province = '';
this.city = '';
this.custLeader = '';
this.custMobile = '';
},
onCancel(){
this.show = false;

this.resetFunc();
},
onSearch(){
this.show = false;
this.search();
this.resetFunc();
},
search(){

function stringJudge(arg){
return arg!=''?':'+arg:'';
}
let queryString = 'FCustClass'+stringJudge(this.custType)+';CustDQ'+stringJudge(this.province)+';CustCity'+stringJudge(this.city)+';CustLeader'+stringJudge(this.custLeader)+';CustMobile'+stringJudge(this.custMobile);
let custFilterParams = {
url: '/BPMTable',
data: {
TID: 'CustInfoManager',
GID: 'Master',
RID: 'M2',
SID: 'S2',
UserName: this.username,
OrderByColum: '',
QueryString: queryString,
PageIndex: '1',
PageSize: '100',
SIMID: this.simId,
MGUID: '',
DGUID: ''
}
}
axiosGet(custFilterParams.url,custFilterParams.data).then(res => {
this.loading = false;
this.customerList = [];
this.customerList = this.customerList.concat(res);
if(this.customerList.length == 0){
this.noData = true;
}else{
this.noData = false;
}
if (this.customerList.length < 10) {
this.$refs.scrollerBottom.disablePullup();
}
this.$nextTick(() => {
this.$refs.scrollerBottom.reset();
})
},err => {
this.$vux.toast.text(err.Message)
})
}
}
}



