
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import * as util from '@/assets/js/util.js'
import { axiosPost,axiosGet } from '@/api'
import Fingerprint from 'fingerprintjs'

export default {
name: 'Login',
components:{
Group,
XInput,
XButton
},
data(){
return {
userName: '',
password: '',
simId: '',
rememberBool: false
}
},
mounted(){
let user = util.getCookie("userName");
let pwd = util.getCookie("password");
let fingerprint = new Fingerprint().get();
this.simId = fingerprint;

if(user != '' && pwd != ''){
this.userName = user;
this.password = pwd;
let params = {
url: '/MAccount/login2',
data:{
Account: this.userName,
Password: this.password,
SIMID: this.simId
}
}
axiosPost(params.url,params.data).then(res => {
sessionStorage.setItem("accessToken",res.Token_type+' '+res.Access_token);
sessionStorage.setItem("userName",this.userName);
this.$vux.toast.text('登录成功');
this.$router.replace({path: '/salesHome'});
},err => {
this.$vux.toast.text(err.Message);
})
}
},
methods:{
rememberUser(ev){
if(!this.userName.trim() || !this.password.trim()){
this.$vux.toast.text('请先输入用户名和密码');
ev.preventDefault();
return
}
this.rememberBool = !this.rememberBool;
if(!this.rememberBool){
util.delCookie("userName");
util.delCookie("password");
}else{
util.setCookie("userName",this.userName,7);
util.setCookie("password",this.password,7);
}
},
handleSubmit(){
if(!this.userName.trim()){
this.$vux.toast.text('请输入用户名');
return
}
if(!this.password.trim()){
this.$vux.toast.text('请输入密码');
return
}
this.$vux.loading.show({
text: '登录中'
})

let params_rongyun = {
url: 'http://60.174.226.164:21513/api/user/login',
data:{
username: this.userName,
password: '',
nickname: '',
portraitUri: ''
},
dataType: "json"
}
axiosPost(params_rongyun.url,params_rongyun.data,params_rongyun.dataType).then(res=>{
let token_rongyun = res.token;
sessionStorage.setItem("token_rongyun",token_rongyun);
let params = {
url: '/MAccount/login2',
data:{
Account: this.userName,
Password: this.password,
SIMID: this.simId
}
}
axiosPost(params.url,params.data).then(res => {
this.$vux.loading.hide();
sessionStorage.setItem("accessToken",res.Token_type+' '+res.Access_token);
sessionStorage.setItem("userId",this.userName);
sessionStorage.setItem("simId",this.simId);
this.$vux.toast.text('登录成功');
if(this.$route.query.redirect){
let redirect = this.$route.query.redirect;
this.$router.push(redirect);
}else{
this.$router.push({path: '/home'});
}
},err => {
this.$vux.loading.hide();
this.$vux.toast.text(err.Message);
})
},err => {
this.$vux.toast.text(err.Message);
})
}
}
}



