
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Search from 'vux/src/components/search/index.vue'

import * as util from '@/assets/js/util.js'
import { axiosPost,axiosGet } from '@/api'

export default {
name: 'SelectSearch',
directives: {
TransferDom
},
components: {Popup,Search},
data(){
return {
username:'',
simId: '',
show: false,
results: [],
dynamicResults: [],
value: '',
valChoosed: '',
valShow: '',
noData: false,
pageIndex: '1',
PageSize: '20',
custName: ''
}
},
created(){
util.EventUtil.listenTouchDirection(document, false, this.touchUp, '', '', '')
},
mounted(){
this.valShow = sessionStorage.getItem('custNameForBsn');
this.getCustInfo();
},
methods:{
getCustInfo(){
this.username = sessionStorage.getItem('userId');
this.simId = sessionStorage.getItem('simId');

let customerParams = {
url: '/BPMTable',
data: {
TID: 'CustInfoManager',
GID: 'Master',
RID: 'M6',
SID: 'YHJJ_KHXX',
UserName: this.username,
Orderbycolum: '',
Querystring: '',
PageIndex: '1',
PageSize: '50',
SIMID: this.simId,
MGUID: '',
DGUID: '',
}
}
axiosGet(customerParams.url,customerParams.data).then(res => {
this.results = [];
res.forEach((item) => {
let temp = {};
temp.title =  item.CustMobile + ' - '+ item.CustLeader;
temp.key = item.MGUID;
this.results.push(temp);
});
this.dynamicResults = this.results;
this.$vux.loading.hide();
},err => {
this.$vux.toast.text(err.Message)
})
},
getResult(val){

if(val){
this.pageIndex = '1';

let customerFilterParams = {
url: '/BPMTable',
data: {
Proc:'UserMobile_CustInfoNewCX',
Parms: 'String;PageIndex;PageSize',
Values: val + ';' + this.pageIndex + ';' + this.PageSize
}
}
this.$vux.loading.show({
text: '正在搜索...'
})
axiosGet(customerFilterParams.url,customerFilterParams.data).then(res => {
this.dynamicResults = [];
res.forEach((item) => {
let temp = {};
temp.title =  item.CustMobile + ' - '+ item.CustLeader;
temp.key = item.MGUID;
this.dynamicResults.push(temp);
});
if(this.dynamicResults.length == 0){
this.noData = true;
}
this.$vux.loading.hide();
},err => {
this.$vux.loading.hide();
this.$vux.toast.text(err.Message);
})
}else{
this.getCustInfo();
}
},
touchUp(){
let container = document.querySelector(".vux-search_show");
let distance = container.scrollHeight - container.scrollTop;

if(this.value && distance < 1000){
this.pageIndex++;

let customerFilterParams = {
url: '/BPMTable',
data: {
Proc:'UserMobile_CustInfoNewCX',
Parms: 'String;PageIndex;PageSize',
Values: this.value + ';' + this.pageIndex + ';' + this.PageSize
}
}
axiosGet(customerFilterParams.url,customerFilterParams.data).then(res => {
res.forEach((item) => {
let temp = {};
temp.title =  item.CustMobile + ' - '+ item.CustLeader;
temp.key = item.MGUID;
this.dynamicResults.push(temp);
});
},err => {
this.$vux.toast.text(err.Message)
})
}
},
resultClick(item){
this.valChoosed = item.key;
let temp = item.title.split(" - ");
this.valShow = temp[1];
this.$emit('valChange',[this.valChoosed,this.valShow]);
this.show = false;
},
showPop(){
this.show = true;
},
onCancel(){
this.show = false;
}
}
}



