
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import CustomerSelect from '@/components/CustomerSelect'
import { axiosPost,axiosGet } from '@/api'

export default {
name: 'AddBusiness',
data(){
return{
username: '',
simId: '',
customerList: [], //客户姓名
customer: '', // 客户姓名
customerMguid: '',
defaultValueList: [
{
title:'手机：',
data: ''
},{
title:'公司名称：',
data: ''
},{
title:'客户类型：',
data: ''
},{
title:'客户来源：',
data: ''
},{
title:'所属省份：',
data: ''
},{
title:'所属城市：',
data: ''
},{
title:'QQ：',
data: ''
},{
title:'微信：',
data: ''
}
],
CustMobile: '', // 手机
CustName: '', // 公司名称
CustInfoManager_FCustClass: '', // 客户类型
CustInfoManager_FCustLY: '', // 客户来源
CustInfoManager_CustDQ: '', // 所属省份
CustInfoManager_CustCity: '', // 所属城市
Contact: '', // QQ+微信
emergencyDegreeList:[{key:'b0', value:'即时'},{key:'b1', value:'急售'},{key:'b2', value:'一周'},{key:'b4', value:'一个月'}], //紧急度列表
emergency: '', // 紧急度
customerPrice: '',//客户报价
deviceTypeList: [],//设备类型
deviceType: '',
deviceParamsArr: [],
demandBrandsList: [], //需求品牌
demandBrands: '',
brandsParamsArr: [],
machineModelList: [], //型号
machineModel: '',
dateProduction: '', //出厂年限
serviceTime: '', //使用时间
certificateList: [{key:'8310', value:'有'},{key:'8320', value:'无'}], //有无合格证
certificateBool: '',
invoiceList: [{key:'8210', value:'有'},{key:'8220', value:'无'}], //有无发票
invoiceBool: '',
stateList: [{key:'中国', value:'中国'}], //国家或地区
state: '',
provinceList: [], //设备省份
province: '',
cityList : [], //设备城市
city: '',
forecastPrice: '',//预估价格
remark: '' 
}
},
components:{GroupTitle, Group, Cell, XInput, Selector, PopupPicker, XNumber, XTextarea, CustomerSelect},
mounted(){
this.username = sessionStorage.getItem('userId');
this.simId = sessionStorage.getItem('simId');

let deviceParams = {
url: '/BPMTable',
data: {
TID: 'InvenItemClass',
GID: 'Master',
RID: 's1',
SID: 's1',
UserName: this.username,
SIMID: this.simId,
MGUID: '',
DGUID: '',
OrderByColum: '',
QueryString: '',
PageIndex: '1',
PageSize: '100'
}
}
axiosGet(deviceParams.url,deviceParams.data).then(res => {
res.forEach((item) => {
let temp = {};
temp.key = item.ItemClassId +' '+ item.MGUID;
temp.value = item.ItemClassDesc;
this.deviceTypeList.push(temp);
});
},err => {
this.$vux.toast.text(err.Message)
})


let provinceParams = {
url: '/BPMTable',
data: {
TID: 'SSX_Proince',
GID: 'Master',
RID: 'R1',
SID: 'S1',
UserName: this.username,
OrderByColum: '',
QueryString: '',
PageIndex: '1',
PageSize: '100',
SIMID: this.simId,
MGUID: '',
DGUID: ''
}
}
axiosGet(provinceParams.url,provinceParams.data).then(res => {
res.forEach((item) => {
let temp = {};
temp.key = item.Value;
temp.value = item.Value;
this.provinceList.push(temp);
});
},err => {
this.$vux.toast.text(err.Message)
})


this.customerMguid = sessionStorage.getItem('custMguid');
if(this.customerMguid){
this.customer = sessionStorage.getItem('custNameForBsn');
this.fetchData();
}
},
methods:{
getCustomerInfo(msg){
this.customerMguid = msg[0];
this.customer = msg[1];
this.fetchData();
},
fetchData(){
let customerParams = {
url: '/BPMTable',
data: {
TID: 'CustInfoManager',
GID: 'Master',
RID: 'M6',
SID: 'YHJJ_KHXX',
UserName: this.username,
Orderbycolum: '',
Querystring: '',
PageIndex: '1',
PageSize: '20',
SIMID: this.simId,
MGUID: this.customerMguid,
DGUID: '',
}
}
axiosGet(customerParams.url,customerParams.data).then(res => {
let result = res[0];
this.defaultValueList = [
{
title:'手机：',
data: result.CustMobile
},{
title:'公司名称：',
data: result.CustName
},{
title:'客户类型：',
data: result.CustInfoManager_FCustClass
},{
title:'客户来源：',
data: result.CustInfoManager_FCustLY
},{
title:'所属省份：',
data: result.CustInfoManager_CustDQ
},{
title:'所属城市：',
data: result.CustInfoManager_CustCity
},{
title:'QQ：',
data: result.QQ
},{
title:'微信：',
data: result.WXH
}
]
this.CustMobile = result.CustMobile;
this.CustName = result.CustName;
this.CustInfoManager_FCustClass = result.CustInfoManager_FCustClass;
this.CustInfoManager_FCustLY = result.CustInfoManager_FCustLY;
this.CustInfoManager_CustDQ = result.CustInfoManager_CustDQ;
this.CustInfoManager_CustCity = result.CustInfoManager_CustCity;
this.Contact = result.QQ +' '+ result.WXH;
},err => {
this.$vux.toast.text(err.Message)
})
},
chooseDevice(){

this.deviceParamsArr = this.deviceType.split(" ");
let brandParams = {
url: '/BPMTable',
data: {
TID: 'InvenItemClass',
GID: 'Detail2',
RID: 'R1',
SID: 'S1',
UserName: this.username,
SIMID: this.simId,
MGUID: this.deviceParamsArr[1],
DGUID: '',
OrderByColum: '',
QueryString: '',
PageIndex: '1',
PageSize: '300'
}
}
axiosGet(brandParams.url,brandParams.data).then(res => {
this.demandBrandsList = [];
res.forEach((item) => {
let temp = {};
temp.key = item.NewCode +' '+item.DGUID;
temp.value = item.BrandClassDesc;
this.demandBrandsList.push(temp);
});
},err => {
this.$vux.toast.text(err.Message)
})
},
chooseModel(){

this.brandsParamsArr = this.demandBrands.split(" ");
let brandParams = {
url: '/BPMTable',
data: {
TID: 'InvenItemClass',
GID: 'Detail3',
OrderByColum: 'ModelClassDesc+asc',
QueryString: 'NewCode%3a' + this.brandsParamsArr[0],
COLUMNS: 'NewCode,ModelClassDesc'
}
}
axiosGet(brandParams.url,brandParams.data).then(res => {
this.machineModelList = [];
res.forEach((item) => {
let temp = {};
temp.key = item.ModelClassDesc;
temp.value = item.ModelClassDesc;
this.machineModelList.push(temp);
});
},err => {
this.$vux.toast.text(err.Message)
})
},





chooseCity(){

let provinceParams = {
url: '/BPMTable',
data: {
TID: 'system_city',
GID: 'Master',
RID: 'R1',
SID: 'S1',
UserName: this.username,
OrderByColum: '',
QueryString: 'shen%3a'+this.province,
PageIndex: '1',
PageSize: '100',
SIMID: this.simId,
MGUID: '',
DGUID: ''
}
}
axiosGet(provinceParams.url,provinceParams.data).then(res => {
this.cityList = [];
res.forEach((item) => {
let temp = {};
temp.key = item.c_name;
temp.value = item.c_name;
this.cityList.push(temp);
});
},err => {
this.$vux.toast.text(err.Message)
})
},
handleSubmit(){

let empty = '';
switch (empty){
case this.customer:
this.$vux.toast.text('请选择客户');
break;
case this.emergency:
this.$vux.toast.text('请选择紧急度');
break;
case this.customerPrice:
this.$vux.toast.text('请输入客户报价');
break;
case this.deviceType:
this.$vux.toast.text('请选择设备类型');
break;
case this.demandBrands:
this.$vux.toast.text('请选择品牌');
break;
case this.machineModel:
this.$vux.toast.text('请选择型号');
break;
case this.dateProduction:
this.$vux.toast.text('请输入出厂年限');
break;
case this.serviceTime:
this.$vux.toast.text('请输入使用时间');
break;
default:
let params = {
url: '/BPMTable',
data:{
TID: "BuyNewCompeteBsnis",
GID: "Master",
RID: "R1",
SID: "S1",
UserName: this.username,
SIMID: this.simId,
MGUID: "null",
UserMobile: "UserMobile_BuyXZ",
keyandvalue: [
{
field:"SBLX",value:this.deviceParamsArr[0]
},{
field:"Pbrands",value:this.brandsParamsArr[0]
},{
field:"Pmodel",value:this.machineModel
},{
field:"Pdatep",value:this.dateProduction
},{
field:"Phours",value:this.serviceTime
},{
field:"Certificate",value:this.certificateBool
},{
field:"Invoice",value:this.invoiceBool
},{
field:"GJHDQ",value:this.state
},{
field:"SBSF",value:this.province
},{
field:"SBCS",value:this.city
},{
field:"KeyValue",value:''
},{
field:"CustLeader",value:this.customer
},{
field:"CustPhone",value:this.CustMobile
},{
field:"Contact",value:this.Contact
},{
field:"CustName",value:this.CustName
},{
field:"CustClass",value:this.CustInfoManager_FCustClass
},{
field:"FCustLY",value:this.CustInfoManager_FCustLY
},{
field:"CustDQ",value:this.CustInfoManager_CustDQ
},{
field:"CustCity",value:this.CustInfoManager_CustCity
},{
field:"Bdates",value:this.emergency
},{
field:"Bprice",value:this.customerPrice
},{
field:"Memo",value:this.remark
}
]
},
dataType: "json"
}
this.$vux.loading.show({
text: '提交中...'
});
axiosPost(params.url,params.data,params.dataType).then(res => {
let group_id = res[0].BsnNo;

let params_rongyun = {
url: 'http://60.174.226.164:21513/api/group/add',
data:{
group_id: group_id,
group_name: '',
group_portraitUri: '',
usernames: [this.username,res[0].supervisor]
},
dataType: "json"
}
axiosPost(params_rongyun.url,params_rongyun.data,params_rongyun.dataType).then(res=>{
console.log(res);
},err => {
this.$vux.toast.text(err.Message)
})

let sharedTime = dateFormat(new Date(), 'YYYY-MM-DD HH:mm:ss');
let params_business_share = {
url: '/BPMTable',
data:{
GID: "Detail2",
MGUID: res[0].MGUID,
RID: "R1",
SID: "S2",
SIMID: this.simId,
TID: "BuyNewCompeteBsnis",
UserMobile: "UserMobile_buyBsnisMDB2XZ",
UserName: this.username,
keyandvalue: [
{
field: "BGXR",
value: res[0].supervisor
},{
field: "GXR",
value: this.username
},{
field: "GXSJ",
value: sharedTime
}
]
},
dataType: 'json'
}
axiosPost(params_business_share.url,params_business_share.data,params_business_share.dataType).then(res => {
this.$vux.loading.hide();
this.$vux.toast.text('新增商机成功');
this.$router.push({path: '/consignBusiness'});
},err => {
this.$vux.loading.hide();
this.$vux.toast.text(err.Message);
})
},err => {
this.$vux.loading.hide();
this.$vux.toast.text(err.Message);
})
}
}
},
destroyed: function () {
sessionStorage.setItem('custMguid','');
sessionStorage.setItem('custNameForBsn','');
}
}



