
import Scroller from 'vux/src/components/scroller/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Confirm from 'vux/src/components/confirm/index.vue'

import { axiosPost,axiosGet } from '@/api'

const pulldownDefaultConfig = {
content: '下拉刷新',
height: 40,
autoRefresh: false,
downContent: '下拉刷新',
upContent: '释放后刷新',
loadingContent: '正在刷新...',
clsPrefix: 'xs-plugin-pulldown-'
}
const pullupDefaultConfig = {
content: '上拉加载更多',
pullUpHeight: 60,
height: 40,
autoRefresh: false,
downContent: '释放后加载',
upContent: '上拉加载更多',
loadingContent: '加载中...',
clsPrefix: 'xs-plugin-pullup-'
}

export default {
name: 'CustomerVisit',
directives: {
TransferDom
},
data(){
return{
username: '',
simId: '',
operateBool: true,
showMenus:false,
menus: {
needMore: '<span>我要更多</span>',
},
loading: true,
noData: false,
pullupDefaultConfig: pullupDefaultConfig,
pulldownDefaultConfig: pulldownDefaultConfig,
pageIndex: 1,
customerVisitList:[],
warningShow: false,
warningContent: ''
}
},
components:{ Scroller,Flexbox,FlexboxItem,Actionsheet,TransferDom,Confirm },
mounted(){
this.username = sessionStorage.getItem('userId');
this.simId = sessionStorage.getItem('simId');
this.fetchData();
},
methods:{
fetchData(){
let params = {
url: '/BPMTable',
data:{
TID: 'CustVisit',
GID: 'Master',
RID: 'R1',
SID: 'S1',
UserName: this.username,
Orderbycolum: 'SRSJ+desc',
Querystring: '',
PageIndex: this.pageIndex,
PageSize: '10',
SIMID: this.simId,
MGUID: '',
DGUID: '',
}
}
axiosGet(params.url,params.data).then(res => {
this.loading = false;
this.customerVisitList = this.customerVisitList.concat(res);
if (this.customerVisitList.length < 10 || res.length == 0) {
this.$refs.scrollerBottom.disablePullup();
}else{
this.$refs.scrollerBottom.enablePullup();
}
if(this.customerVisitList.length == 0){
this.noData = true;
}else{
this.noData = false;
}
this.$nextTick(() => {
this.$refs.scrollerBottom.reset();
})
},err => {
this.$vux.toast.text(err.Message);
})
},
menuListClick(key) {
if(key == 'needMore'){
const params = {
url: "/BPMTable",
data: {
Proc: 'UserMobile_CustVisitZDMoreMXZ',
Parms: 'UserName',
Values: 'shaolong.li'
}
}
axiosGet(params.url,params.data).then(res => {
console.log(res);
},err => {
this.warningShow = true;
this.warningContent = err.Message;
})


}
},
refresh(){
this.pageIndex = 1;
this.customerVisitList = [];
this.fetchData();
this.$nextTick(() => {
this.$refs.scrollerBottom.donePulldown();
})
},
loadMore(){
this.pageIndex++;
this.fetchData();
this.$nextTick(() => {
this.$refs.scrollerBottom.donePullup();
})
},
back(){
this.$router.push({path: '/salesHome'})
}
}
}



