
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import * as util from '@/assets/js/util.js'
import { axiosPost,axiosGet } from '@/api'

export default {
name: 'SalesHome',
data(){
return {

listTitle: ['业务管理'],
index: 0,
summaryData: null,
planAndCompleteMapData: null,
salesFunnelMapData: null,
username: ''
}
},
components:{ Swiper,SwiperItem,Tab,TabItem,Flexbox,FlexboxItem },
mounted(){
this.username = sessionStorage.getItem('userId');
this.drawLine();
},
methods:{
drawLine(){

let planAndCompleteMap = this.$echarts.init(document.getElementById('planAndCompleteMap'));
let planAndCompleteMapParams = {
url: '/BPMTable',
data:{
Proc:'UserMobile_SellWinBsnisWC',
Parms:'UserName',
Values: this.username

}
}
axiosGet(planAndCompleteMapParams.url,planAndCompleteMapParams.data).then(res => {
if(res = []){
this.planAndCompleteMapData = [0,0,0,0];
}else{
this.planAndCompleteMapData = [res[0].TargetDDWeek,res[0].DDNumWeek,res[0].TargetDDMonth,res[0].DDNumMonth];
}
planAndCompleteMap.setOption({
title: {
text: '本周本月的计划与完成(销售)',
textStyle:{
fontWeight:'normal',
fontSize:14
},
left: 'center'
},
grid:{
top:40,
bottom:80
},
tooltip: {},
xAxis: {
data: ["本周订单目标","本周完成订单","本月订单目标","本月完成订单"],
axisLabel: {
interval:0,
rotate:50
},
},
yAxis: {},
series: [{
name: '销量',
type: 'bar',
barWidth: '30%',
itemStyle:{
normal:{
label: {
show: true, //开启显示
position: 'top', //在上方显示
textStyle: { //数值样式
color: '#666',
fontSize: 12
}
},
color:'#188df0',
}
},
data: this.planAndCompleteMapData,
}]
});
},err => {
this.$vux.toast.text(err.Message);
})


let summaryParams = {
url: '/BPMTable',
data:{
Proc: 'UserMobile_SellTitle',
Parms: 'Puser',
Values: this.username
}
}
axiosGet(summaryParams.url,summaryParams.data).then(res => {
if(res.length == 0){
this.summaryData = {
MonGZ: 0,
FinishNum: 0,
MonBFQDNum: 0,
FinishMoney: 0
};
}else{
this.summaryData = res[0];
}
},err => {
this.$vux.toast.text(err.Message);
})


let salesFunnelMap = this.$echarts.init(document.getElementById('salesFunnelMap'));
let salesFunnelMapParams = {
url: '/BPMTable',
data:{
Proc:'UserMobile_ESSBSaleTarMonth',
Parms:'UserName',
Values: this.username

}
}
axiosGet(salesFunnelMapParams.url,salesFunnelMapParams.data).then(res => {
if(res.length == 0){
var userName = this.username;
this.salesFunnelMapData = [
{name: '新增客户', value: 0},
{name: '新增商机', value: 0},
{name: '商机跟踪', value: 0},
{name: '赢单商机', value: 0}
]
}else{
var userName = res[0].USERNAME;
this.salesFunnelMapData = [
{name: '新增客户', value: res[0].CustNum},
{name: '新增商机', value: res[0].BsnisNum},
{name: '商机跟踪', value: res[0].GZNum},
{name: '赢单商机', value: res[0].YDNum}
]
}
salesFunnelMap.setOption({
title: {
text: userName + '的销售漏斗图',
textStyle:{
fontWeight:'normal',
fontSize:14
},
left: 'center'
},
legend: {
orient: 'vertical',
data: ['新增客户','新增商机','商机跟踪','赢单商机'],
top: 30,
right: 10
},
calculable: true,
series: [
{
name:'漏斗图',
type:'funnel',
left: 20,
top: 30,
bottom: 40,
width: '40%',
min: 0,
max: 100,
minSize: '0%',
maxSize: '100%',
sort: 'descending',
label: {
normal: {
show: true,
formatter: '{c}',   // 金字塔外标签
position: 'outside'
}
},
labelLine: {
normal: {
length: 10,
lineStyle: {
width: 1,
type: 'solid'
}
}
},
itemStyle: {
normal: {
borderColor: '#fff',
borderWidth: 1
}
},
data: this.salesFunnelMapData
}
]
});
},err => {
this.$vux.toast.text(err.Message);
})
},
back(){
this.$router.push({path: '/home'})
}
}
}



