

import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
import 'babel-polyfill'
import FastClick from 'fastclick'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import VueAMap from 'vue-amap';
import echarts from 'echarts'

Vue.config.productionTip = false




Vue.use(ToastPlugin)
Vue.use(LoadingPlugin)
Vue.use(VueAMap)


VueAMap.initAMapApiLoader({

key: 'e8d6b83091076c5352ebfc7f8c764687',

plugin: ['AMap.Geolocation']
});


router.beforeEach((to, from, next) => {
if (to.matched.some(record => record.meta.requireAuth)){ // 判断该路由是否需要登录权限
if (sessionStorage.accessToken) { // 判断当前的accessToken是否存在 ； 登录存入的accessToken
next();
}else {
next({
path: '/',
query: {redirect: to.fullPath} 
})
}
}else {
next();
}
});

Vue.component('inline-loading', InlineLoading)
Vue.component('x-header', XHeader)
Vue.component('x-button', XButton)

Vue.prototype.$echarts = echarts

/* eslint-disable no-new */
new Vue({
el: '#app',
router,
store,
components: { App },
template: '<App/>',
beforeCreate () {
Vue.prototype.bus = this
}
})



