
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import * as util from '@/assets/js/util.js'
import { axiosPost,axiosGet,axiosPut } from '@/api'

export default {
name: 'AddCustomer',
data(){
return {
username: '',
simId: '',
companyName: '',  //公司名称
custAddress: '',  //客户地址
custTypeList: [],  //客户类型
custType: '',
productTypeList: [],  //产品类别
productType: '',
productTypeArr: [],
brandsList: [],  //品牌
brand: '',
deviceParamsArr: '',
khyxlList: [{key:'Y', value:'是'},{key:'N', value:'否'}],  //区域影响客户
khyxl: '',
like: '',  //喜好
positionList: [],  //职位
position: '',
custLeader: '',    //姓名
custMobile: '',    //手机
custMobile1: '',    //手机2
custSourceList: [],  //客户来源
custSource: '',
idCard: '',  //身份证号码
qq: '',  //qq号
weChat: '',  //微信号
provinceList: [],  //省份
province: '',
cityList: [],  //城市
city: '',
remark: '',  //备注
custMobileBool: false,
custMguid: '',
title: '客户添加'
}
},
components:{GroupTitle, Group, Cell, XInput, Selector, PopupPicker, XNumber, XTextarea},
mounted(){
this.username = sessionStorage.getItem('userId');
this.simId = sessionStorage.getItem('simId');
this.custMguid = sessionStorage.getItem('custMguid');


let custParams = {
url: '/BPMTable',
data: {
TID: 'CustomerType',
GID: 'Master',
RID: 'S1',
SID: 's1',
UserName: this.username,
SIMID: this.simId,
MGUID: '',
DGUID: '',
OrderByColum: '',
QueryString: '',
PageIndex: '1',
PageSize: '100'
}
}
axiosGet(custParams.url,custParams.data).then(res => {
res.forEach((item) => {
let temp = {};
temp.key = item.TypeId;
temp.value = item.TypeDesc;
this.custTypeList.push(temp);
});
},err => {
this.$vux.toast.text(err.Message)
})


let deviceParams = {
url: '/BPMTable',
data: {
TID: 'InvenItemClass',
GID: 'Master',
RID: 's1',
SID: 's1',
UserName: this.username,
SIMID: this.simId,
MGUID: '',
DGUID: '',
OrderByColum: '',
QueryString: '',
PageIndex: '1',
PageSize: '100'
}
}
axiosGet(deviceParams.url,deviceParams.data).then(res => {
res.forEach((item) => {
let temp = {};
temp.key = item.ItemClassId +' '+ item.MGUID;
temp.value = item.ItemClassDesc;
this.productTypeList.push(temp);
});
},err => {
this.$vux.toast.text(err.Message)
})


let positionParams = {
url: '/BPMTable',
data: {
TID: 'Identity',
GID: 'Master',
RID: 'r01',
SID: 's01',
UserName: this.username,
OrderByColum: '',
QueryString: '',
PageIndex: '1',
PageSize: '100',
SIMID: this.simId,
MGUID: '',
DGUID: ''
}
}
axiosGet(positionParams.url,positionParams.data).then(res => {
res.forEach((item) => {
let temp = {};
temp.key = item.Id;
temp.value = item.DIdentity;
this.positionList.push(temp);
});
},err => {
this.$vux.toast.text(err.Message)
})


let custSourceParams = {
url: '/BPMTable',
data: {
ID:'CustLY'
}
}
axiosGet(custSourceParams.url,custSourceParams.data).then(res => {
res.forEach((item) => {
let temp = {};
temp.key = item.LDGUID;
temp.value = item.DESC0;
this.custSourceList.push(temp);
});
},err => {
this.$vux.toast.text(err.Message)
})


let provinceParams = {
url: '/BPMTable',
data: {
TID: 'SSX_Proince',
GID: 'Master',
RID: 'R1',
SID: 'S1',
UserName: this.username,
OrderByColum: '',
QueryString: '',
PageIndex: '1',
PageSize: '100',
SIMID: this.simId,
MGUID: '',
DGUID: ''
}
}
axiosGet(provinceParams.url,provinceParams.data).then(res => {
res.forEach((item) => {
let temp = {};
temp.key = item.Value;
temp.value = item.Value;
this.provinceList.push(temp);
});
},err => {
this.$vux.toast.text(err.Message)
})


if(this.custMguid != ''){

this.title = sessionStorage.getItem('custEditTitle');
let params = {
url: '/BPMTable',
data:{
TID:'CustInfoManager',
GID:'Master',
RID:'M5',
SID:'S2',
UserName:this.username,
SIMID: this.simId,
MGUID: this.custMguid,
DGUID:'',
Orderbycolum:'',
Querystring:'',
PageIndex:1,
PageSize:10
}
}
axiosGet(params.url,params.data).then(res => {
let temp = res[0];
let cplb = this.productTypeList.filter((item,index)=>{
let keyArr = item.key.split(' ');
return keyArr[0] == temp.CPLB;
})
this.companyName = temp.CustName;   
this.custAddress = temp.CustAddress || '';   
this.custType = temp.FCustClass;    
cplb.length != 0 ? this.productType=cplb[0].key : this.productType='';
this.brand = temp.PinPai || '';  
this.khyxl = temp.KHYXL || '';  
this.like = temp.XiHao || ''; 
this.position = temp.DLSSF || '';  
this.custLeader = temp.CustLeader; 
this.custMobile = temp.CustMobile;  
this.custMobile1 = temp.CustMobile1 || ''; 
this.custSource = temp.FCustLY; 
this.idCard = temp.SFZNo || ''; 
this.qq = temp.QQ || ''; 
this.weChat = temp.WXH || ''; 
this.province = temp.CustInfoManager_CustDQ; 
this.city = temp.CustInfoManager_CustCity; 
this.remark = temp.CustMemo || ''; 
},err => {
this.$vux.toast.text(err.Message)
})
}else{
return;
}
},
methods:{
chooseDevice(){

this.productTypeArr = this.productType.split(" ");
let brandParams = {
url: '/BPMTable',
data: {
TID: 'InvenItemClass',
GID: 'Detail2',
RID: 'R1',
SID: 'S1',
UserName: this.username,
SIMID: this.simId,
MGUID: this.productTypeArr[1],
DGUID: '',
OrderByColum: '',
QueryString: '',
PageIndex: '1',
PageSize: '300'
}
}
axiosGet(brandParams.url,brandParams.data).then(res => {
this.brandsList = [];
res.forEach((item) => {
let temp = {};
temp.key = item.NewCode;
temp.value = item.BrandClassDesc;
this.brandsList.push(temp);
});
},err => {
this.$vux.toast.text(err.Message)
})
},
checkMobile(){
if(this.custMobile == ''){
this.$vux.toast.text('请输入手机号');
return
}
if(util.phoneReg(this.custMobile)){
this.$vux.toast.text('手机号码格式不正确');
return
}

let phoneParams = {
url: '/BPMTable',
data: {
TID: 'CustInfoManager',
GID: 'Master',
RID: 'M3',
SID: 'S2',
UserName: this.username,
OrderByColum: '',
QueryString: 'CustMobile%3A'+ this.custMobile,
PageIndex: '1',
PageSize: '100',
SIMID: this.simId,
MGUID: '',
DGUID: ''
}
}
this.$vux.loading.show({
text: '检测中...'
});
axiosGet(phoneParams.url,phoneParams.data).then(res => {
this.$vux.loading.hide();
if(res.length == 0){
this.$vux.toast.text('该手机号可用');
}else{
this.$vux.toast.text('该手机号已经存在，请输入其他手机号');
}
},err => {
this.$vux.loading.hide();
this.$vux.toast.text(err.Message)
})
},
chooseCity(){

let provinceParams = {
url: '/BPMTable',
data: {
TID: 'system_city',
GID: 'Master',
RID: 'R1',
SID: 'S1',
UserName: this.username,
OrderByColum: '',
QueryString: 'shen%3a'+this.province,
PageIndex: '1',
PageSize: '100',
SIMID: this.simId,
MGUID: '',
DGUID: ''
}
}
axiosGet(provinceParams.url,provinceParams.data).then(res => {
this.cityList = [];
res.forEach((item) => {
let temp = {};
temp.key = item.c_name;
temp.value = item.c_name;
this.cityList.push(temp);
});
},err => {
this.$vux.toast.text(err.Message)
})
},
handleSubmit(){

let empty = '';
switch (empty){
case this.companyName:
this.$vux.toast.text('请输入公司名称');
break;
case this.custType:
this.$vux.toast.text('请选择客户类型');
break;
case this.custLeader:
this.$vux.toast.text('请输入姓名');
break;
case this.custMobile:
this.$vux.toast.text('请输入手机号');
break;
case this.custSource:
this.$vux.toast.text('请选择客户来源');
break;
case this.province:
this.$vux.toast.text('请选择所属省份');
break;
case this.city:
this.$vux.toast.text('请选择所属城市');
break;
default:
if(!util.phoneReg(this.custMobile)){
this.$vux.toast.text('手机号码格式不正确');
return
}
if(this.custMobile1 && !util.phoneReg(this.custMobile1)){
this.$vux.toast.text('手机2号码格式不正确');
return
}
if(!util.nameReg(this.custLeader)){
this.$vux.toast.text('姓名不正确');
return
}
if(this.idCard && !util.idCardReg(this.idCard)){
this.$vux.toast.text('身份证格式不正确');
return
}
this.$vux.loading.show({
text: '提交中...'
})
if(this.custMguid!= ''){

let submitParams = {
url: '/BPMTable',
data:{
TID: "CustInfoManager",
GID: "Master",
RID: "M5",
SID: "S2",
MGUID: this.custMguid,
UserName: this.username,
SIMID: this.simId,
TIDKeyValue: this.custMguid,
UserMobile: "UserMobile_CustInfoMBXG",
keyandvalue: [
{
field:"CustLeader",value:this.custLeader  //姓名
},{
field:"FCustLY",value:this.custSource  //客户来源
},{
field:"CustName",value:this.companyName  //公司名称
},{
field:"CustAddress",value:this.custAddress  //客户地址
},{
field:"CustDQ",value:this.province  //客户所属省份
},{
field:"CustCity",value:this.city  //客户所属城市
},{
field:"FCustClass",value:this.custType  //客户类型
},{
field:"CPLB",value:this.productTypeArr[0]  //客户产品类别
},{
field:"PinPai",value:this.brand  //品牌
},{
field:"KHYXL",value:this.khyxl  //区域影响客户
},{
field:"XiHao",value:this.like  //喜好
},{
field:"DLSSF",value:this.position  //职位
},{
field:"CustMobile",value:this.custMobile  //手机
},{
field:"CustMobile1",value:this.custMobile1  //手机2
},{
field:"SFZNo",value:this.idCard  //身份证号码
},{
field:"CustPhone",value:this.custMobile  //电话
},{
field:"QQ",value:this.qq  //qq号
},{
field:"CustSaleMan",value:this.username  //业务员
},{
field:"wfsyscuser",value:this.username  //经办人
},{
field:"WXH",value:this.weChat  //微信号
},{
field:"CustMemo",value:this.remark  //备注
}
]
},
dataType: 'json'
}
axiosPut(submitParams.url,submitParams.data,submitParams.dataType).then(res => {
this.$vux.loading.hide();
this.$vux.toast.text('提交成功');
this.$router.push({path: '/customer'});
},err => {
this.$vux.loading.hide();
this.$vux.toast.text(err.Message);
})
}else{

let phoneParams = {
url: '/BPMTable',
data: {
TID: 'CustInfoManager',
GID: 'Master',
RID: 'M3',
SID: 'S2',
UserName: this.username,
OrderByColum: '',
QueryString: 'CustMobile%3A'+ this.custMobile,
PageIndex: '1',
PageSize: '100',
SIMID: this.simId,
MGUID: '',
DGUID: ''
}
}
axiosGet(phoneParams.url,phoneParams.data).then(res => {
if(res.length == 0){

let submitParams = {
url: '/BPMTable',
data:{
GID: "Master",
RID: "M5",
SID: "S2",
SIMID: this.simId,
TID: "CustInfoManager",
UserName: this.username,
MGUID: "",
DGUID: "",
UserMobile: "UserMobile_CustInfoMBXZ",
keyandvalue: [
{
field:"CustLeader",value:this.custLeader  //姓名
},{
field:"FCustLY",value:this.custSource  //客户来源
},{
field:"CustName",value:this.companyName  //公司名称
},{
field:"CustAddress",value:this.custAddress  //客户地址
},{
field:"CustDQ",value:this.province  //客户所属省份
},{
field:"CustCity",value:this.city  //客户所属城市
},{
field:"FCustClass",value:this.custType  //客户类型
},{
field:"CPLB",value:this.productTypeArr[0]  //客户产品类别
},{
field:"PinPai",value:this.brand  //品牌
},{
field:"KHYXL",value:this.khyxl  //区域影响客户
},{
field:"XiHao",value:this.like  //喜好
},{
field:"DLSSF",value:this.position  //职位
},{
field:"CustMobile",value:this.custMobile  //手机
},{
field:"CustMobile1",value:this.custMobile1  //手机2
},{
field:"SFZNo",value:this.idCard  //身份证号码
},{
field:"CustPhone",value:this.custMobile  //电话
},{
field:"QQ",value:this.qq  //qq号
},{
field:"CustSaleMan",value:this.username  //业务员
},{
field:"wfsyscuser",value:this.username  //经办人
},{
field:"WXH",value:this.weChat  //微信号
},{
field:"CustMemo",value:this.remark  //备注
}
]
},
dataType: 'json'
}
axiosPost(submitParams.url,submitParams.data,submitParams.dataType).then(res => {
this.$vux.loading.hide();
this.$vux.toast.text('提交成功');
this.$router.push({path: '/customer'});
},err => {
this.$vux.loading.hide();
this.$vux.toast.text(err.Message);
})
}else{
this.$vux.loading.hide();
this.$vux.toast.text('该手机号已经存在，请输入其他手机号');
}
},err => {
this.$vux.loading.hide();
this.$vux.toast.text(err.Message)
})
}
}
}
},
destroyed: function () {
sessionStorage.setItem('custMguid','');
}
}



