
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Checklist from 'vux/src/components/checklist/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import * as util from '@/assets/js/util.js'
import { axiosPost,axiosGet } from '@/api'

export default {
name:'GroupMemberSelect',
directives: {
TransferDom
},
props:['groupId','bsn-mguid'],
data(){
return {
simId: '',
userId: '',
show: false,
noData: false,
personlist: [],
personlistValue: []
}
},
components:{ Popup,TransferDom,Checklist,PopupHeader },
mounted(){
this.simId = sessionStorage.getItem("simId");
this.userId = sessionStorage.getItem("userId");

let params = {
url: '/BPMTable',
data:{
TID:'UserBranch',
GID:'Master',
RID:'R1',
SID:'Sell',
UserName:'',
Orderbycolum:'USERNAME+asc',
PageIndex:1,
PageSize:100,
SIMID: this.simId,
MGUID: '',
DGUID:'',
Querystring:''
}
}
axiosGet(params.url,params.data).then(res => {
res.forEach((item,i) => {
let temp = {};
temp.key = item.UserCode;
temp.value = item.USERNAME +' ['+ item.UserBranch_Branch + ']';
this.personlist.push(temp);
});
},err => {
this.$vux.toast.text(err.Message)
})
},
methods:{
showPop(){
this.show = true;

let paramsGetMembers = {
url: 'http://60.174.226.164:21513/api/group/getMembers',
data:{
group_id: this.groupId
}
}
axiosGet(paramsGetMembers.url,paramsGetMembers.data).then(res=>{
let tempArr = [];
res.forEach((item,index) => {
tempArr.push(item.username);
});
this.personlist = this.personlist.filter((item,index) => {
return tempArr.indexOf(item.key) == '-1'
})
},err => {
this.$vux.toast.text(err.Message)
})
},
onCancel(){
this.show = false;
},
onChoosed(){

let sharedTime = dateFormat(new Date(), 'YYYY-MM-DD HH:mm:ss');
let params_business_share = {
url: '/BPMTable',
data:{
GID: "Detail2",
MGUID: this.bsnMguid,
RID: "R1",
SID: "S2",
SIMID: this.simId,
TID: "SellNewCompeteBsnis",
UserMobile: "UserMobile_SellBsnisMDB2XZ",
UserName: "null",
keyandvalue: [
{
field: "BGXR",
value: this.personlistValue[0]
},
{
field: "GXR",
value: this.userId
},
{
field: "GXSJ",
value: sharedTime
}
]
},
dataType: 'json'
}
axiosPost(params_business_share.url,params_business_share.data,params_business_share.dataType).then(res => {

let paramsAddMembers = {
url: 'http://60.174.226.164:21513/api/group/join',
data:{
"usernames": this.personlistValue,
"group_id": this.groupId,
"group_name": ""
},
dataType: 'json'
}
axiosPost(paramsAddMembers.url,paramsAddMembers.data,paramsAddMembers.dataType).then(res=>{
this.$vux.toast.text('添加成员成功');
this.personlistValue = [];
},err => {
this.$vux.toast.text(err.Message)
})
},err => {
this.$vux.loading.hide();
this.$vux.toast.text(err.Message);
})
this.onCancel();
}
}
}



