
import XButton from 'vux/src/components/x-button/index.vue'

import * as util from '@/assets/js/util.js'
import { axiosPost } from '@/api'

export default {
name: "Upload",
data() {
return {
showFace: false,
imgList: [],
size: 0,
limit:6, //限制图片上传的数量
tempImgs:[],
bigImgShow: false,
indexNow: ''
}
},
components:{
XButton
},
created(){
util.EventUtil.listenTouchDirection(document, true, ' ', this.right, ' ', this.left)
},
methods: {
chooseType() {
this.$refs.upload_file.click();
},
fileChange(el) {
if (!el.target.files[0].size) return;
this.fileList(el.target);
el.target.value = ''
},
fileList(fileList) {
let files = fileList.files;
for (let i = 0; i < files.length; i++) {

if (files[i].type != '') {
let imgListName = [];
this.imgList.forEach(item => {
imgListName.push(item.file.name)
})
if(imgListName.indexOf(files[i].name) == -1){
this.fileAdd(files[i]);
}
} else {

this.folders(fileList.items[i]);
}
}
},

folders(files) {
let _this = this;

if (files.kind) {
files = files.webkitGetAsEntry();
}
files.createReader().readEntries(function (file) {
for (let i = 0; i < file.length; i++) {
if (file[i].isFile) {
_this.foldersAdd(file[i]);
} else {
_this.folders(file[i]);
}
}
});

},
foldersAdd(entry) {
let _this = this;
entry.file(function (file) {
_this.fileAdd(file)
})
},
fileAdd(file) {
if (this.limit !== undefined) this.limit--;
if (this.limit !== undefined && this.limit < 0) return;

this.size = this.size + file.size;

if (file.type.indexOf('image') == -1) {
this.$vux.toast.text('请选择图片文件')
} else {
let reader = new FileReader();
let image = new Image();
let _this = this;

let canvas = document.createElement('canvas');
let context = canvas.getContext('2d');

reader.readAsDataURL(file); 

reader.onload = function (e) {

file.src = e.target.result;
image.onload = function(){
let width = image.width;
let height = image.height;
file.width = width;
file.height = height;


let originWidth = width;
let originHeight = height;

let maxWidth = 400, maxHeight = 400;

let targetWidth = originWidth, targetHeight = originHeight;

if (originWidth > maxWidth || originHeight > maxHeight) {
if (originWidth / originHeight > maxWidth / maxHeight) {

targetWidth = maxWidth;
targetHeight = Math.round(maxWidth * (originHeight / originWidth));
} else {
targetHeight = maxHeight;
targetWidth = Math.round(maxHeight * (originWidth / originHeight));
}
}


canvas.width = targetWidth;
canvas.height = targetHeight;

context.clearRect(0, 0, targetWidth, targetHeight);

context.drawImage(image, 0, 0, targetWidth, targetHeight);

context.drawImage(_this.$refs.img_cover, 0, 0, 200, 200, targetWidth/2,targetHeight/2, 200, 200);


canvas.toBlob(function (blob) {
let newImg = document.createElement("img"),
url = URL.createObjectURL(blob);






console.log(url)

newImg.src = url;
document.body.appendChild(newImg);

}, file.type || 'image/png');

_this.imgList.push({
file
});
};
image.src = file.src;
}
}
},
delImg(index) {
this.size = this.size - this.imgList[index].file.size;
this.imgList.splice(index, 1);
if (this.limit !== undefined) this.limit = 6-this.imgList.length;
},
displayImg(index) {
this.bigImgShow = !this.bigImgShow;
this.$refs.bigImg.src = this.imgList[index].file.src;
this.indexNow = index;
},
hideImg(){
this.bigImgShow = !this.bigImgShow;
},
left(){
if(this.indexNow < this.imgList.length-1){
this.indexNow++
this.$refs.bigImg.src = this.imgList[this.indexNow].file.src;
}
},
right(){
if(this.indexNow > 0){
this.indexNow--
this.$refs.bigImg.src = this.imgList[this.indexNow].file.src;
}
},
uploadFile(){
console.log(this.imgList);
}
}
}



