
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import { axiosPost,axiosGet } from '@/api'

export default {
name: "BusinessShare",
data(){
return {
username: '',
userId: '',
businessInfo: null,
personlist: [],
personShared: null,
bsnMguid:''
}
},
components:{Flexbox,FlexboxItem,Selector,Group,XButton},
mounted(){
this.simId = sessionStorage.getItem("simId");
this.username = sessionStorage.getItem("userId");
this.businessInfo = [
{
title: "商机编号：",
data: sessionStorage.getItem("bsnNoShared")
},{
title: "商机信息：",
data: sessionStorage.getItem("totalMsgShared")
},{
title: "手机：",
data: sessionStorage.getItem("custPhoneShared")
},
]

let params = {
url: '/BPMTable',
data:{
TID:'UserBranch',
GID:'Master',
RID:'R1',
SID:'Buy',
UserName:this.username,
SIMID: this.simId,
MGUID: '',
DGUID:'',
Orderbycolum:'USERNAME+asc',
Querystring:'UserDEP:07',
PageIndex:1,
PageSize:100
}
}
axiosGet(params.url,params.data).then(res => {
res.forEach((item,i) => {
let temp = {};
temp.key = item.UserCode;
temp.value = item.USERNAME +' ['+ item.UserBranch_Branch + ']';
this.personlist.push(temp);
});
},err => {
this.$vux.toast.text(err.Message)
})
},
methods:{
handleSubmit(){
let personShared = this.$refs.personSelect.getFullValue();
let sharedTime = dateFormat(new Date(), 'YYYY-MM-DD HH:mm:ss');
this.bsnMguid = this.$route.query.id;
if(!personShared){
this.$vux.toast.text('请选择被分享人');
return
}
let params = {
url: '/BPMTable',
data:{
GID: "Detail2",
MGUID: this.bsnMguid,
RID: "R1",
SID: "S2",
SIMID: this.simId,
TID: "BuyNewCompeteBsnis",
UserMobile: "UserMobile_buyBsnisMDB2XZ",
UserName: this.username,
keyandvalue: [
{
field: "BGXR",
value: personShared[0].key
},
{
field: "GXR",
value: this.username
},
{
field: "GXSJ",
value: sharedTime
}
]
},
dataType: 'json'
}
this.$vux.loading.show({
text: '提交中...'
});
axiosPost(params.url,params.data,params.dataType).then(res => {

let paramsAddMembers = {
url: 'http://60.174.226.164:21513/api/group/join',
data:{
"usernames": [personShared[0].key],
"group_id": this.businessInfo[0].data,
"group_name": ""
},
dataType: 'json'
}
axiosPost(paramsAddMembers.url,paramsAddMembers.data,paramsAddMembers.dataType).then(res=>{
console.log(res);
},err => {
this.$vux.toast.text(err.Message)
})
this.$vux.loading.hide();
this.$vux.toast.text('商机共享成功');
this.$router.push({path: '/consignBusiness'});
},err => {
this.$vux.loading.hide();
this.$vux.toast.text(err.Message);
})
}
}
}



