
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Selector from 'vux/src/components/selector/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import { axiosGet,axiosPut } from '@/api'

export default {
name: 'CustomerVisitDetail',
directives: {
TransferDom
},
data(){
return {
simId: '',
username: '',
visitList: [],
mguid: '',
loading: true,
resultList: [{
key:'Busy', value:'客户在忙'
},{
key:'CustKH', value:'空号'
},{
key:'CustWFJT', value:'无法接听'
},{
key:'CustZH', value:'转行'
},{
key:'HaveSJ', value:'有商机'
},{
key:'Nothing', value:'无商机'
}], //回访结果
result: '',
remark: '',
bsnModal: false,
bsnMenu: {
buyMachine: '我有车收',
saleMachine: '我要卖车',
},
}
},
components:{ Flexbox,FlexboxItem,Selector,XTextarea,Actionsheet,TransferDom },
mounted(){
this.mguid = this.$route.query.id;
this.username = sessionStorage.getItem('userId');
this.simId = sessionStorage.getItem('simId');

let params = {
url: '/BPMTable',
data:{
TID: 'CustVisit',
GID: 'Master',
RID: 'R1',
SID: 'S1',
UserName: this.username,
Orderbycolum: 'SRSJ+desc',
Querystring: '',
PageIndex: '1',
PageSize: '10',
SIMID: this.simId,
MGUID: this.mguid,
DGUID: '',
}
}
axiosGet(params.url,params.data).then(res => {
this.loading = false;

this.visitList = [
{
title:'回访人：',
data: res[0].CustVisit_VisitMan
},{
title: '回访岗位：',
data: res[0].CustVisit_VisitGW
},{
title: '输入时间：',
data: res[0].SRSJ
},{
title: '回访时间：',
data: res[0].VisitTime
},{
title: '回访客户：',
data: res[0].CustVisit_VisitCust
},{
title: '回访客户电话：',
data: res[0].VisitMobile,
type: 'phoneNumber'
},{
title: '回访客户类别：',
data: res[0].CustVisit_VisitType
},{
title: '状态：',
data: res[0].ZT
},{
title: '所属省份：',
data: res[0].CustVisit_CustDQ
},{
title: '所属城市：',
data: res[0].CustVisit_CustCity
}
]
this.result = res[0].VisitZT;
this.remark = res[0].Memo;
},err => {
this.$vux.toast.text(err.Message);
})
},
methods:{
handleSubmit(){
if(!this.result){
this.$vux.toast.text('请选择回访结果');
return
}

const params = {
url: '/BPMTable',
data: {
GID: "Master",
RID: "R1",
SID: "S1",
SIMID: this.simId,
TID: "CustVisit",
TIDKeyValue: this.mguid,
UserMobile: "UserMobile_CustVisitMWC",
UserName: "null",
keyandvalue: [
{
field: "VisitZT",
value: this.result
},{
field: "Memo",
value: this.remark
},{
field: "ZT",
value: "完成"
}
]
},
dataType:'json'
}
this.$vux.loading.show({
text: '提交中...'
})
axiosPut(params.url,params.data,params.dataType).then(res => {
this.$vux.loading.hide();
if(this.result != 'HaveSJ'){
this.$vux.toast.text('提交成功');
this.$router.go(-1);
}else{
this.bsnModal = true;
}
},err => {
this.$vux.loading.hide();
this.$vux.toast.text(err.Message);
})
},
bsnChoose(key){
if(key == 'buyMachine'){
this.$vux.toast.text('暂无寄售商机');
}
if(key == 'saleMachine'){
this.$router.push({ path: '/addBusiness',query:{ id:this.mguid }});
}
}
}
}



