
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import { axiosPost,axiosGet } from '@/api'

export default {
name: 'AddBankInfo',
data(){
return {
username: '',
simId: '',
custMguid: '',
CAccount: '',
CAccountNum: '',
Bank: ''
}
},
components:{ Group,XInput },
mounted(){
this.username = sessionStorage.getItem('userId');
this.simId = sessionStorage.getItem('simId');
this.custMguid = sessionStorage.getItem('custMguid');
},
methods:{
onSubmit(){
let empty = '';
switch (empty){
case this.CAccount:
this.$vux.toast.text('请输入收款账户');
break;
case this.CAccountNum:
this.$vux.toast.text('请输入收款账号');
break;
case this.Bank:
this.$vux.toast.text('请输入开户行');
break;
default:
let reg = /^([1-9]{1})(\d{14}|\d{18})$/gi;
if(!reg.test(this.CAccountNum)){
this.$vux.toast.text('银行卡格式不正确');
return
}

let bankParams = {
url: '/BPMTable',
data: {
GID: "Detail1",
RID: "R1",
SID: "s1",
TID: "CustInfoManager",
SIMID: this.simId,
UserName: this.username,
MGUID: this.custMguid,  //客户编号
DGUID: "Null",
keyandvalue:[
{
field: "CAccount",
value: this.CAccount
},{
field: "CAccountNum",
value: this.CAccountNum
},{
field: "Bank",
value: this.Bank
}
]
},
dataType: 'json'
}
this.$vux.loading.show({
text: '提交中...'
})
axiosPost(bankParams.url,bankParams.data,bankParams.dataType).then(res => {
this.$vux.loading.hide();
this.$vux.toast.text('添加成功');
this.$router.push({path: '/CustomerDetail',query:{ id:this.custMguid }});
},err => {
this.$vux.loading.hide();
this.$vux.toast.text(err.Message)
})
}
}
}
}



