
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Group from 'vux/src/components/group/index.vue'
import Search from 'vux/src/components/search/index.vue'

import { axiosPost,axiosGet } from '@/api'

export default {
name: 'ShortDataSearch',
directives: {
TransferDom
},
components: {Popup,Group,Search},
data(){
return {
username:'',
simId: '',
show: false,
results: [],
dynamicResults: [],
value: '',
valChoosed: '',
valShow: '',
noData: false,
pageIndex: 1,
PageSize: 10,
}
},
mounted(){
this.username = sessionStorage.getItem('userId');
this.simId = sessionStorage.getItem('simId');

let brandParams = {
url: '/BPMTable',
data: {
TID: 'InvenItemClass',
GID: 'Detail2',
RID: 'R1',
SID: 'S1',
UserName: this.username,
SIMID: this.simId,
MGUID: '02D6DBE3-9ACE-4FAA-833D-66470D811F74',
DGUID: '',
OrderByColum: '',
QueryString: '',
PageIndex: '1',
PageSize: '1000'
}
}
axiosGet(brandParams.url,brandParams.data).then(res => {
res.forEach((item) => {
let temp = {};
temp.title = item.BrandClassDesc;
temp.key = item.NewCode;
this.results.push(temp);
});
this.dynamicResults = this.results;
},err => {
this.$vux.toast.text(err.Message)
})
},
methods:{
showPop(){
this.show = true;
},
getResult(val){
console.log('监听输入值', val);
let results_filter = this.results.filter((item,index) => {
return item.title.indexOf(val) != '-1'
});
this.dynamicResults = results_filter;
this.dynamicResults.length == 0 ? this.noData = true : this.noData = false;
},
resultClick(item){
this.valChoosed = item.key;
this.valShow = item.title;
this.show = false;
},
onCancel(){
this.show = false;
}
}
}



