
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'

import GroupChat from '@/components/GroupChat'
import GroupMemberSelect from '@/components/GroupMemberSelect'
import MachineNumSelect from '@/components/MachineNumSelect'
import { axiosPost,axiosGet,axiosPut } from '@/api'

export default {
name: 'AddBusiness',
directives: {
TransferDom
},
data(){
return{
simId: '',
username: '',
operateBool: true,
masterList: [],
bsnNo: '',
bsnMguid: '',
listTitle:['跟踪明细'],
index:0,
loading:true,
showMenus:false,
menus: {
btnEditBsn: '<span>编辑</span>',
btnLose: '<span>丢单</span>',
btnAbandon: '<span>放弃</span>',
btnShare: '<span>共享</span>',
btnGet: '<span>赢单</span>'
},
show:false,
machineNum: '',
price: '',
financingList: [{key:'Y', value:'是'},{key:'N', value:'否'}], //是否融资
financingBool: '',
externalBsnCostList: [{key:'Y', value:'是'},{key:'N', value:'否'}], //外部商机费
externalBsnCostBool:'',
loseModalShow:false,
abandonModalShow:false
}
},
mounted(){
this.bsnMguid = this.$route.query.id;
this.username = sessionStorage.getItem('userId');
this.simId = sessionStorage.getItem('simId');
let params = {
url: '/BPMTable',
data:{
TID:'SellNewCompeteBsnis',
GID:'Master',
RID:'R1',
SID:'S1',
UserName:'',
Orderbycolum:'',
PageIndex:1,
PageSize:10,
SIMID: this.simId,
MGUID: this.bsnMguid,
DGUID:'',
Querystring:''
}
}
axiosGet(params.url,params.data).then(res => {
this.loading = false;
this.bsnNo = res[0].BsnNo;
this.CustPhone = res[0].CustPhone;

let bsnStatus = res[0].ST;
if(bsnStatus == '丢单' || bsnStatus == '赢单' || bsnStatus == '放弃' || bsnStatus == '关闭商机'){
this.operateBool = false;
document.querySelector('.add-icon').style.display='none';
}else{
this.operateBool = true;
if(bsnStatus == '待跟踪'){
delete this.menus.btnGet;
}
}

this.masterList = [
{
title:'商机编号：',
data: res[0].BsnNo
},
{
title: '经办人：',
data: res[0].SellNewCompeteBsnis_Puser
},
{
title: '姓名：',
data: res[0].CustLeader
},
{
title: '客户手机：',
data: res[0].CustPhone,
type: 'phoneNumber'
},
{
title: '公司名称：',
data: res[0].CustName
},
{
title: '状态：',
data: res[0].ST
},
{
title: '紧急度：',
data: res[0].SellNewCompeteBsnis_Bdates
},
{
title: '期望购买地点：',
data: res[0].Plocation
},
{
title: '设备类型：',
data: res[0].SellNewCompeteBsnis_SBLX
},
{
title: '需求品牌1：',
data: res[0].SellNewCompeteBsnis_Brand
},
{
title: '需求品牌2：',
data: res[0].SellNewCompeteBsnis_Probrand2
},
{
title: '需求品牌3：',
data: res[0].SellNewCompeteBsnis_Probrand3
},
{
title: '出厂年限：',
data: res[0].SellNewCompeteBsnis_Pdatep
},
{
title: '使用时间：',
data: res[0].SellNewCompeteBsnis_Phours
},
{
title: '价格区间：',
data: res[0].SellNewCompeteBsnis_Prices
},
{
title: '成交价格(元)：',
data: res[0].CJJJ
},
{
title: '机械属性：',
data: res[0].KeyValue
},
{
title: '商机来源：',
data: res[0].SellNewCompeteBsnis_SJLY
},
{
title: '是否融资：',
data: res[0].IsFinancing == 'Y'?'是':'否'
},
{
title: '机器序列号：',
data: res[0].JQXLH
},
{
title: '外部商机费：',
data: res[0].WBNum
},
{
title: '备注：',
data: res[0].Memo
}
]
},err => {
this.$vux.toast.text(err.Message)
})
this.$router.afterEach((to, from) => {
this.$router.go(0);
})
},
components:{ Flexbox,FlexboxItem,Swiper,SwiperItem,Tab,TabItem,GroupChat,GroupMemberSelect,Actionsheet,TransferDom,PopupHeader,Group,Selector,XInput,Popup,MachineNumSelect,Confirm },
methods:{
menuListClick(key) {
sessionStorage.setItem('bsnMguid',this.bsnMguid);
switch(key){
case 'btnEditBsn':
sessionStorage.setItem('bsnEditTitle','商机编辑');
this.$router.push({ path: '/businessEdit',query:{ id:this.bsnMguid }});
break;
case 'btnLose':
this.loseModalShow = true;
break;
case 'btnAbandon':
this.abandonModalShow = true;
break;
case 'btnShare':
sessionStorage.setItem("bsnNoShared",this.bsnNo);
sessionStorage.setItem("custPhoneShared",this.CustPhone);
this.$router.push({ path: '/businessShare',query:{ id:this.bsnMguid }});
break;
case 'btnGet':
this.show = true;
break;
default:
return
}
},
loseBsnConfirm(value){
if(!value){
this.$vux.toast.text('请输入丢单原因');
return
}
this.$refs.loseModal.setInputValue('');

let bsnLoseParams = {
url: '/BPMTable',
data: {
GID: "Master",
RID: "R1",
SID: "S1",
SIMID: this.simId,
TID: "SellNewCompeteBsnis",
TIDKeyValue: this.bsnMguid,
UserMobile: "UserMobile_SellBsnisMDD",
UserName: "null",
keyandvalue: [
{
field: "lostreason",
value: value
}
]
},
dataType:'json'
}
this.$vux.loading.show({
text: '提交中...'
})
axiosPut(bsnLoseParams.url,bsnLoseParams.data,bsnLoseParams.dataType).then(res => {
this.$vux.loading.hide();
this.$router.push({path: '/business'});
this.$vux.toast.text('提交成功');
},err => {
this.$vux.loading.hide();
this.$vux.toast.text(err.Message);
})
},
abandonBsnConfirm(value){
if(!value){
this.$vux.toast.text('请输入放弃原因');
return
}
this.$refs.abandonModal.setInputValue('');

let bsnLoseParams = {
url: '/BPMTable',
data: {
GID: "Master",
RID: "R1",
SID: "S1",
SIMID: this.simId,
TID: "SellNewCompeteBsnis",
TIDKeyValue: this.bsnMguid,
UserMobile: "UserMobile_SellBsnisMDD",
UserName: "null",
keyandvalue: [
{
field: "lostreason",
value: value
}
]
},
dataType:'json'
}
this.$vux.loading.show({
text: '提交中...'
})
axiosPut(bsnLoseParams.url,bsnLoseParams.data,bsnLoseParams.dataType).then(res => {
this.$vux.loading.hide();
this.$router.push({path: '/business'});
this.$vux.toast.text('提交成功');
},err => {
this.$vux.loading.hide();
this.$vux.toast.text(err.Message);
})
},
getMachineNum(msg){
this.machineNum = msg;
},
onCancel(){
this.price = '';
this.financingBool = '';
this.externalBsnCostBool = '';
this.show = false;
},
onSubmit(){

let empty = '';
switch (empty){
case this.machineNum:
this.$vux.toast.text('请选择机器序列号');
break;
case this.price:
this.$vux.toast.text('请填写成交价格');
break;
case this.externalBsnCostBool:
this.$vux.toast.text('请选择外部商机费');
break;
default:
this.show = false;

let submitParams = {
url: '/BPMTable',
data: {
GID: "Master",
RID: "R1",
SID: "S1",
SIMID: this.simId,
TID: "SellNewCompeteBsnis",
TIDKeyValue: this.bsnMguid,
UserMobile: "UserMobile_SellBsnisMYD",
UserName: "null",
keyandvalue: [
{
field: "YDR",
value: this.username
},{
field: "JQXLH",
value: this.machineNum
},{
field: "CJJJ",
value: this.price
},{
field: "IsFinancing",
value: this.financingBool
},{
field: "WBNum",
value: this.externalBsnCostBool
}
]
},
dataType:'json'
}
this.$vux.loading.show({
text: '提交中...'
})
axiosPut(submitParams.url,submitParams.data,submitParams.dataType).then(res => {
console.log(res);
this.$vux.loading.hide();
this.$router.push({path: '/business'});
this.$vux.toast.text('提交成功');
},err => {
this.$vux.loading.hide();
this.$vux.toast.text(err.Message);
})
}
}
}
}



