
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import CustomerSelect from '@/components/CustomerSelect'
import { axiosPost,axiosGet,axiosPut } from '@/api'

export default {
name: 'AddBusiness',
data(){
return{
username: '',
simId: '',
bsnMguid: '',
custName: '', // 公司名称
customerPrice: '', //客户报价
deviceTypeList: [], //设备类型
deviceType: '',
deviceParamsArr: [],
demandBrandsList: [], //品牌
demandBrands: '',
brandsParamsArr: [],
machineModelList: [], //型号
machineModel: '',
repositoryList: [], //收货仓库
repository: '',
dateProduction: '',
serviceTime: '',
certificateList: [{key:'8310', value:'有'},{key:'8320', value:'无'}], //有无合格证
certificateBool: '',
invoiceList: [{key:'8210', value:'有'},{key:'8220', value:'无'}], //有无发票
invoiceBool: '',
stateList: [{key:'中国', value:'中国'}], //国家或地区
state: '',
provinceList: [], //设备省份
province: '',
cityList : [], //设备城市
city: '',
remark: '' 
}
},
components:{GroupTitle, Group, Cell, XInput, Selector, PopupPicker, XNumber, XTextarea, CustomerSelect},
mounted(){
this.username = sessionStorage.getItem('userId');
this.simId = sessionStorage.getItem('simId');
this.bsnMguid = this.$route.query.id;

let deviceParams = {
url: '/BPMTable',
data: {
TID: 'InvenItemClass',
GID: 'Master',
RID: 's1',
SID: 's1',
UserName: this.username,
SIMID: this.simId,
MGUID: '',
DGUID: '',
OrderByColum: '',
QueryString: '',
PageIndex: '1',
PageSize: '100'
}
}
axiosGet(deviceParams.url,deviceParams.data).then(res => {
res.forEach((item) => {
let temp = {};
temp.key = item.ItemClassId +' '+ item.MGUID;
temp.value = item.ItemClassDesc;
this.deviceTypeList.push(temp);
});
},err => {
this.$vux.toast.text(err.Message)
})

let provinceParams = {
url: '/BPMTable',
data: {
TID: 'SSX_Proince',
GID: 'Master',
RID: 'R1',
SID: 'S1',
UserName: this.username,
OrderByColum: '',
QueryString: '',
PageIndex: '1',
PageSize: '100',
SIMID: this.simId,
MGUID: '',
DGUID: ''
}
}
axiosGet(provinceParams.url,provinceParams.data).then(res => {
res.forEach((item) => {
let temp = {};
temp.key = item.Value;
temp.value = item.Value;
this.provinceList.push(temp);
});
},err => {
this.$vux.toast.text(err.Message)
})

let repository = {
url: '/BPMTable',
data: {
TID: 'InveWarehouse',
GID: 'Master',
RID: 'R1',
SID: 'S1',
UserName: this.username,
SIMID: this.simId,
MGUID: '',
DGUID: '',
OrderByColum: '',
QueryString: '',
PageIndex: '1',
PageSize: '100',
}
}
axiosGet(repository.url,repository.data).then(res => {
res.forEach((item) => {
let temp = {};
temp.key = item.WareId;
temp.value = item.Warehouse;
this.repositoryList.push(temp);
});
},err => {
this.$vux.toast.text(err.Message);
})

let bsnParams = {
url: '/BPMTable',
data:{
TID:'BuyNewCompeteBsnis',
GID:'Master',
RID:'R1',
SID:'S1',
UserName:'',
Orderbycolum:'',
PageIndex:1,
PageSize:10,
SIMID: this.simId,
MGUID: this.bsnMguid,
DGUID:'',
Querystring:''
}
}
axiosGet(bsnParams.url,bsnParams.data).then(res => {
let temp = res[0];
let sblx = this.deviceTypeList.filter((item,index)=>{
let keyArr = item.key.split(' ');
return keyArr[0] == temp.SBLX;
})
this.custName = temp.CustName; 
this.customerPrice = temp.Bprice; 
sblx.length != 0 ? this.deviceType=sblx[0].key : this.deviceType='';
this.demandBrands = temp.Pbrands;
this.machineModel = temp.Pmodel;
this.repository = temp.IINV;
this.dateProduction = temp.Pdatep;
this.serviceTime = temp.Phours;
this.certificateBool = temp.Certificate || '';
this.invoiceBool = temp.Invoice || '';
this.state = '中国';
this.province = temp.SBSF;
this.city = temp.SBCS;
this.remark = temp.Memo || '';
},err => {
this.$vux.toast.text(err.Message);
})
},
methods:{
chooseDevice(){

this.deviceParamsArr = this.deviceType.split(" ");
let brandParams = {
url: '/BPMTable',
data: {
TID: 'InvenItemClass',
GID: 'Detail2',
RID: 'R1',
SID: 'S1',
UserName: this.username,
SIMID: this.simId,
MGUID: this.deviceParamsArr[1],
DGUID: '',
OrderByColum: '',
QueryString: '',
PageIndex: '1',
PageSize: '300'
}
}
axiosGet(brandParams.url,brandParams.data).then(res => {
this.demandBrandsList = [];
res.forEach((item) => {
let temp = {};
temp.key = item.NewCode;
temp.value = item.BrandClassDesc;
this.demandBrandsList.push(temp);
});
},err => {
this.$vux.toast.text(err.Message)
})
},
chooseCity(){

let provinceParams = {
url: '/BPMTable',
data: {
TID: 'system_city',
GID: 'Master',
RID: 'R1',
SID: 'S1',
UserName: this.username,
OrderByColum: '',
QueryString: 'shen%3a'+this.province,
PageIndex: '1',
PageSize: '100',
SIMID: this.simId,
MGUID: '',
DGUID: ''
}
}
axiosGet(provinceParams.url,provinceParams.data).then(res => {
this.cityList = [];
res.forEach((item) => {
let temp = {};
temp.key = item.c_name;
temp.value = item.c_name;
this.cityList.push(temp);
});
},err => {
this.$vux.toast.text(err.Message)
})
},
chooseModel(){

this.brandsParamsArr = this.demandBrands.split(" ");
let brandParams = {
url: '/BPMTable',
data: {
TID: 'InvenItemClass',
GID: 'Detail3',
OrderByColum: 'ModelClassDesc+asc',
QueryString: 'NewCode%3a' + this.brandsParamsArr[0],
COLUMNS: 'NewCode,ModelClassDesc'
}
}
axiosGet(brandParams.url,brandParams.data).then(res => {
this.machineModelList = [];
res.forEach((item) => {
let temp = {};
temp.key = item.ModelClassDesc;
temp.value = item.ModelClassDesc;
this.machineModelList.push(temp);
});
},err => {
this.$vux.toast.text(err.Message)
})
},

handleSubmit(){
let empty = '';
switch (empty){
case this.demandBrands:
this.$vux.toast.text('请选择品牌');
break;
case this.machineModel:
this.$vux.toast.text('请选择型号');
break;
case this.repository:
this.$vux.toast.text('请选择收货仓库');
break;
case this.dateProduction:
this.$vux.toast.text('请输入出厂年限');
break;
case this.serviceTime:
this.$vux.toast.text('请输入使用时间');
break;
default:
let bsnEditParams = {
url: '/BPMTable',
data: {
GID: "Master",
RID: "R1",
SID: "S1",
SIMID: this.simId,
TID: "BuyNewCompeteBsnis",
TIDKeyValue: this.bsnMguid,
UserMobile: "UserMobile_BuyBsnisMXG",
UserName: this.username,
keyandvalue: [
{
field: "Bprice",
value: this.customerPrice
},{
field: "SBLX",
value: this.deviceParamsArr[0]
},{
field: "Pbrands",
value: this.demandBrands
},{
field: "Pmodel",
value: this.machineModel
},{
field: "IINV",
value: this.repository
},{
field: "Pdatep",
value: this.dateProduction
},{
field: "Phours",
value: this.serviceTime
},{
field: "Certificate",
value: this.certificateBool
},{
field: "Invoice",
value: this.invoiceBool
},{
field: "GJHDQ",
value: this.state
},{
field: "SBSF",
value: this.province
},{
field: "SBCS",
value: this.city
},{
field: "Memo",
value: this.remark
}
]
},
dataType:'json'
}
this.$vux.loading.show({
text: '提交中...'
})
axiosPut(bsnEditParams.url,bsnEditParams.data,bsnEditParams.dataType).then(res => {
this.$vux.loading.hide();
this.$vux.toast.text('提交成功');
this.$router.go(-1);
},err => {1
this.$vux.loading.hide();
this.$vux.toast.text(err.Message);
})
}
}
}
}



