// sessionStorage
import instance from 'axios'

export const session = function(key, value) {
  if (value === void 0) {
    var lsVal = sessionStorage.getItem(key)
    if (lsVal && lsVal.indexOf('autostringify-') === 0) {
      return JSON.parse(lsVal.split('autostringify-')[1])
    } else {
      return lsVal
    }
  } else {
    if (typeof value === 'object' || Array.isArray(value)) {
      value = 'autostringify-' + JSON.stringify(value)
    }
    return sessionStorage.setItem(key, value)
  }
}
// 手机号验证
export const phoneReg = function(num) {
  let reg = /^1[3456789]\d{9}$/gi
  if (reg.test(num)) {
    return true
  } else {
    return false
  }
}
// 姓名验证
export const nameReg = function(num) {
  let reg = /^[\u4e00-\u9fa5]{2,20}$/
  if (reg.test(num)) {
    return true
  } else {
    return false
  }
}
// 身份证验证
export const idCardReg = function(num) {
  let reg = /^\d{6}(18|19|20)?\d{2}(0[1-9]|1[12])(0[1-9]|[12]\d|3[01])\d{3}(\d|X)$/i 
  if (reg.test(num)) {
    return true
  } else {
    return false
  }
}
export const numberReg = function(num) {
  let reg = /\D+/gi
  if (reg.test(num)) {
    return false
  } else {
    return true
  }
}

// ajax错误处理
export const catchError = function(error) {
  if (error.response) {
    // 401 session为空  402token时效 403token为空
    let arr = [401, 402, 403, 404]
    let msg = {
      '401': 'session为空',
      '402': 'token失效',
      '403': 'token为空'
    }
    if (arr.indexOf(parseInt(error.response.status)) > -1) {
      if (process.env.NODE_ENV !== 'production') {
        console.log(parseInt(error.response.status))
      }
      if (
        process.env.NODE_ENV !== 'production' ||
        process.env.ERROR_FLAG === 'test'
      ) {
        console.log(
          msg[parseInt(error.response.status)] + '本地session已经清除'
        )
      } else {
        sessionStorage.removeItem('token')
        sessionStorage.removeItem('sessionid')
        location.reload()
      }
    } else {
      console.warn(error)
    }
  }
  return Promise.reject(error)
}

// 统一格式化数据接口
export const dataFormate = function ConvertToTreeGridJson(
  rows,
  idFieldName,
  pidFieldName,
  fileds,
  isCallBack
) {
  function exists(rows, ParentId) {
    for (var i = 0; i < rows.length; i++) {
      if (rows[i][idFieldName] === ParentId) {
        return true
      }
    }
    return false
  }

  var nodes = []
  // get the top level nodes
  for (var i = 0; i < rows.length; i++) {
    var row = rows[i]
    if (!exists(rows, row[pidFieldName])) {
      var data = {
        id: row[idFieldName]
      }
      var arrFiled = fileds.split(',')
      for (var j = 0; j < arrFiled.length; j++) {
        if (arrFiled[j] !== idFieldName) {
          data[arrFiled[j]] = row[arrFiled[j]]
        }
      }
      nodes.push(data)
    }
  }
  // console.info('根目录nodes：' + JSON.stringify(nodes))
  var toDo = []
  for (let i = 0; i < nodes.length; i++) {
    toDo.push(nodes[i])
  }
  let count = 1
  toDo.push(count)
  while (toDo.length) {
    var node = toDo.shift() // the parent node
    if (isCallBack) {
      if (node === 1) {
        toDo.push(++count)
      }
      if (node === 2) {
        toDo.map(item => {
          item.isEnd = true
        })
        toDo.push(++count)
      }
      if (node === 3) {
        toDo.push(++count)
      }
    }
    // get the children nodes
    for (let i = 0; i < rows.length; i++) {
      let row = rows[i]
      if (row[pidFieldName] === node.id) {
        var child = {
          id: row[idFieldName]
        }
        let arrFiled = fileds.split(',')
        for (let j = 0; j < arrFiled.length; j++) {
          if (arrFiled[j] !== idFieldName) {
            child[arrFiled[j]] = row[arrFiled[j]]
          }
        }
        if (node.children) {
          node.children.push(child)
        } else {
          node.children = [child]
        }
        // 循环寻找children
        toDo.push(child)
      }
    }
  }
  return nodes
}

// 权限过滤
export const filterPropety = function(obj, flag) {
  return obj.some(item => {
    return item.permissionName === flag
  })
}

// 格式化resPeople type 是label attr shi  value
export const formateLists = function(arr, type, attr, callBack) {
  let key2val = []
  if (arr.length) {
    arr.map(item => {
      let obj = {
        value: attr ? item[attr] : item.id,
        label: type ? item[type] : item.realName
      }
      // 可插入其他属性
      callBack && callBack(obj)
      key2val.push(obj)
    })
  }
  return key2val
}
export const formateException = function(num) {
  if (!num) {
    return 0
  } else {
    return num
  }
}
// formate props
export const formateProperty = function(lists, ahead) {
  let obj = []
  if (lists.length) {
    lists.map(item => {
      obj.push(item.permissionName.split(ahead)[1])
    })
  }
  return obj
}

