
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import * as util from '@/assets/js/util.js'
import { axiosPost,axiosGet } from '@/api'

const pulldownDefaultConfig = {
content: '下拉刷新',
height: 40,
autoRefresh: false,
downContent: '下拉刷新',
upContent: '释放后刷新',
loadingContent: '正在刷新...',
clsPrefix: 'xs-plugin-pulldown-'
}
const pullupDefaultConfig = {
content: '上拉加载更多',
pullUpHeight: 60,
height: 40,
autoRefresh: false,
downContent: '释放后加载',
upContent: '上拉加载更多',
loadingContent: '加载中...',
clsPrefix: 'xs-plugin-pullup-'
}

export default {
name: 'ConsignBusiness',
directives: {
TransferDom
},
data(){
return {
businessList: [],
loading: true,
noData: false,
pullupDefaultConfig: pullupDefaultConfig,
pulldownDefaultConfig: pulldownDefaultConfig,
pageIndex: 1,
show: false,
bsnStateList: ['待跟踪','跟踪','丢单','赢单','放弃','关闭商机'],
bsnState:'',
deviceTypeList: [],
deviceType: '',
emergencyDegreeList:[{key:'b0', value:'即时'},{key:'b1', value:'急售'},{key:'b2', value:'一周'},{key:'b4', value:'一个月'}], //紧急度列表
emergency: '',
bsnoOrMobile: '',
username: '',
simId: '',
}
},
components:{ Flexbox,FlexboxItem,XButton,Scroller,Popup, TransferDom, PopupHeader,Group,Selector,XInput },
mounted(){
this.username = sessionStorage.getItem('userId');
this.simId = sessionStorage.getItem('simId')
this.fetchData();

let deviceParams = {
url: '/BPMTable',
data: {
TID: 'InvenItemClass',
GID: 'Master',
RID: 's1',
SID: 's1',
UserName: this.username,
SIMID: this.simId,
MGUID: '',
DGUID: '',
OrderByColum: '',
QueryString: '',
PageIndex: '1',
PageSize: '100'
}
}
axiosGet(deviceParams.url,deviceParams.data).then(res => {
res.forEach((item) => {
let temp = {};
temp.key = item.ItemClassId;
temp.value = item.ItemClassDesc;
this.deviceTypeList.push(temp);
});
},err => {
this.$vux.toast.text(err.Message);
})
},
methods:{
fetchData(){
let params = {
url: '/BPMTable',
data:{
Proc:'UserMobile_BuyMessage',
Parms:'Puser;Search;PageIndex;PageSize;ST;SBLX;Bdates',
Values:this.username +';;'+ this.pageIndex +';'+ 50 +';;;'
}
}
axiosGet(params.url,params.data).then(res => {
this.$nextTick(() => {
this.$refs.scrollerBottom.reset();
})
this.loading = false;
this.businessList = this.businessList.concat(res);
if(this.businessList.length == 0){
this.noData = true;
}
if (this.businessList.length < 10 || res.length == 0) {
this.$refs.scrollerBottom.disablePullup();
}
},err => {
this.$vux.toast.text(err.Message);
})
},
back(){
this.$router.push({path: '/consignHome'});
},
showPop(){
this.show = true;
},
onCancel(){
this.show = false;
},
resetFunc(){
this.bsnState = '';
this.deviceType = '';
this.emergency = '';
this.bsnoOrMobile = '';
},
onSearch(){
this.show = false;
let bsnFilterParams = {
url: '/BPMTable',
data:{
Proc:'UserMobile_BuyMessage',
Parms:'Puser;Search;PageIndex;PageSize;ST;SBLX;Bdates',
Values:this.username +';'+this.bsnoOrMobile+';'+ 1 +';'+ 200 +';'+this.bsnState+';'+this.deviceType+';'+this.emergency
}
}
axiosGet(bsnFilterParams.url,bsnFilterParams.data).then(res => {
this.pageIndex = 1;
this.businessList = [];
this.loading = false;
this.businessList = this.businessList.concat(res);
if(this.businessList.length == 0){
this.noData = true;
}else{
this.noData = false;
}
this.$nextTick(() => {
this.$refs.scrollerBottom.reset({top: 0});
})
if (this.businessList.length < 10) {
this.$refs.scrollerBottom.disablePullup();
}
},err => {
this.$vux.toast.text(err.Message);
})
this.resetFunc();
},
addConsignBusiness(){
this.$router.push({path: '/addConsignBusiness'});
},
refresh(){
this.pageIndex = 1;
this.businessList = [];
this.fetchData();
this.$nextTick(() => {
this.$refs.scrollerBottom.donePulldown();
})
},
loadMore(){
this.pageIndex++;
this.fetchData();
this.$nextTick(() => {
this.$refs.scrollerBottom.donePullup();
})
},
sendSharedMsg(e){
let totalMsg = e.currentTarget.getAttribute('total');
sessionStorage.setItem('totalMsgShared',totalMsg);
},
}
}



